<template>
    <div class="main">
        <div class="top">
            <span>返利总额</span>
            <div class="sum">{{ sum }}</div>
        </div>

        <mt-navbar v-model="selected" >
            <mt-tab-item id="0">全部</mt-tab-item>
            <mt-tab-item id="1">单返</mt-tab-item>
            <mt-tab-item id="2">月返</mt-tab-item>
            <mt-tab-item id="3">季度返</mt-tab-item>
            <mt-tab-item id="4">年返</mt-tab-item>
        </mt-navbar>
        <div class="box">
        <!-- tab-container -->
            <mt-tab-container v-model="selected">
                <mt-tab-container-item id="0">
                    <div class="item" v-for="item in list_0" >
                        <div class="item_top">
                            {{ item.code_info.store_name }} - {{ item.code_info.goods_name }}
                            <span class="item_right">{{ item.pdr_amount }}</span>
                        </div>
                        <div class="item_bottom">
                            {{ item.pdr_add_time }}
                            <span class="item_right"></span>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="1">
                    <div class="item" v-for="item in list_1">
                        <div class="item_top">
                            {{ item.code_info.store_name }} - {{ item.code_info.goods_name }}
                            <span class="item_right">{{ item.pdr_amount }}</span>
                        </div>
                        <div class="item_bottom">
                            {{ item.pdr_add_time }}
                            <span class="item_right"></span>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="2">
                    <div class="item" v-for="item in list_2">
                        <div class="item_top">
                            {{ item.code_info.store_name }} - {{ item.code_info.goods_name }}
                            <span class="item_right">{{ item.pdr_amount }}</span>
                        </div>
                        <div class="item_bottom">
                            {{ item.pdr_add_time }}
                            <span class="item_right"></span>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="3">
                    <div class="item" v-for="item in list_3">
                        <div class="item_top">
                            {{ item.code_info.store_name }} - {{ item.code_info.goods_name }}
                            <span class="item_right">{{ item.pdr_amount }}</span>
                        </div>
                        <div class="item_bottom">
                            {{ item.pdr_add_time }}
                            <span class="item_right"></span>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="4">
                    <div class="item" v-for="item in list_4">
                        <div class="item_top">
                            {{ item.code_info.store_name }} - {{ item.code_info.goods_name }}
                            <span class="item_right">{{ item.pdr_amount }}</span>
                        </div>
                        <div class="item_bottom">
                            {{ item.pdr_add_time }}
                            <span class="item_right"></span>
                        </div>
                    </div>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
    </div>
</template>


<script>
  export default {
    data: function () {
      return {
        selected: '0',
        sum: '',
        list_0: [],
        list_1: [],
        list_2: [],
        list_3: [],
        list_4: []
      }
    },
    created: function () {
      this.$store.commit('common_setTitle', '水厂返利')
      this.refresh()
      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserForbackWaterfee' + this.$store.state.common.userId).then(function (data) {
        this.sum = data.body.data
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
    },
    methods: {
      getdata: function (ste, cb) {
        cb = cb || function () {}
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserForbackWaterfeeList' +
              '&ste=' + ste +
              this.$store.state.common.userId
        ).then(function (data) {
          cb(data.body.data)
        }, function (req) {
          this.$toast(this.$store.state.common.tip.nointnet)
        })
      },
      refresh: function () {
        var _this = this
        _this.getdata(_this.selected, function (data) {
          if (_this.selected === '0') {
            _this.list_0 = data
          } else
          if (_this.selected === '1') {
            _this.list_1 = data
          } else
          if (_this.selected === '2') {
            _this.list_2 = data
          } else
          if (_this.selected === '3') {
            _this.list_3 = data
          } else
          if (_this.selected === '4') {
            _this.list_4 = data
          }
        })
      }
    },
    watch: {
      selected: function () {
        this.refresh()
      }
    }
  }
</script>
<style scoped>
    .top{
        background: url("../image/rebate/rebate_background.png") no-repeat;
        background-size: cover;
        background-position: bottom;
        height: 8rem;
        text-align: center;
        color: white;
        font-size: 0.8rem;
        padding-top: 1.5rem;
        box-sizing: border-box;
    }
    .top .sum{
        margin-top: .5rem;
        font-size: 2rem;
    }
    .box{
        border-top: 0.5rem solid #EBEBEB;
    }
    .item{
        height: 4rem;
        background: white;
        padding: 0.5rem 1rem;
        padding-right: 8rem;
        box-sizing: border-box;
        position: relative;
    }
    .item:nth-child(even){
        background: #F7F7F7;
    }
    .item .item_top{
        height: 1.5rem;
        line-height: 1.5rem;
    }
    .item .item_top .item_right{
        top: 1.2rem;
        color:#F0923E ;
    }
    .item .item_bottom{
        height: 1.5rem;
        line-height: 1.5rem;
        font-size: 0.8rem;
        color: #999;
    }
    .item .item_right{
        position: absolute;
        right: 1rem;
        font-size: 1.2rem;
    }
</style>




