import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/components/Index'
import Custom from '@/components/Custom'
import rebate from '@/components/Rebate'
import PageTransition from '@/components/PageTransition'
import building from '@/components/Building'

// order
// import Order from '@/components/order/Order'
import orderOrderList from '@/components/order/OrderList'
import orderOrderDetail from '@/components/order/OrderDetail'

// address
import address from '@/components/address/Address'
import addressAddressList from '@/components/address/AddressList'
import addressAddressEdit from '@/components/address/AddressEdit'

// service
import service from '@/components/service/Service'
import servicePolicy from '@/components/service/policy/Policy'
import servicePolicyDetail from '@/components/service/policy/PolicyDetail'
// import serviceCustomer from '@/components/service/customer/CustomerList'
import serviceCustomerDetail from '@/components/service/customer/CustomerDetail'
import serviceCollege from '@/components/service/college/College'
import serviceCollegeDetail from '@/components/service/college/CollegeDetail'
// import serviceEmployee from '@/components/service/employee/Employee'
// import serviceShop from '@/components/service/shop/Shop'
// import serviceGoods from '@/components/service/goods/Goods'

// Purchase
import purchase from '@/components/purchase/Purchase'
import purchaseIndex from '@/components/purchase/Index'
import purchaseShoppingCart from '@/components/purchase/ShoppingCart'
import purchaseOrder from '@/components/purchase/OrderList'
import purchaseOrderDetail from '@/components/purchase/OrderDetail'
import purchaseDetailList from '@/components/purchase/DetailList'
// Pay
import pay from '@/components/pay/Pay'
import payPayList from '@/components/pay/PayList'
import payPayType from '@/components/pay/PayType'
import payPayFinish from '@/components/pay/PayFinish'
import payWallet from '@/components/pay/wallet/Wallet'
import payWalletMyWallet from '@/components/pay/wallet/MyWallet'
import payWalletRecharge from '@/components/pay/wallet/RechargeList'
import payWalletRechargePay from '@/components/pay/wallet/RechargePay'
// import payWalletWaterCash from '@/components/pay/wallet/WaterCash'
// import payWalletBalance from '@/components/pay/wallet/Balance'
import payWalletMyaccount from '@/components/pay/wallet/Myaccount'

// News  暂时与学院公用
import news from '@/components/news/News'
// import newsNewsList from '@/components/news/NewsList'

// distribution 分销
import distribution from '@/components/distribution/Distribution'
import distributionDistributionList from '@/components/distribution/DistributionList'

// // 或者你可以新建一个方法
Router.prototype.goback = function () {
  this.isBack = true
  window.history.go(-1)
}
Vue.use(Router)

// 增强原方法，好处是旧的业务模块不需要任何变动
// Router.prototype.goBack = function () {
//   this.isBack = true
//   this.go(-1)
// }

export default new Router({
  routes: [{
    path: '/',
    name: 'PageTransition',
    component: PageTransition,
    children: [{
      path: '',
      name: 'Index',
      component: Index
    },
    {
      path: '/Custom',
      name: 'Custom',
      component: Custom
    },
    {
      path: '/Rebate',
      name: 'Rebate',
      component: rebate
    },
    {
      path: '/Purchase',
      name: 'Puerchase',
      component: purchase,
      children: [{
        path: 'PurchaseIndex',
        name: 'Purchase_Index',
        component: purchaseIndex
      },
      {
        path: 'ShoppingCart',
        name: 'Purchase_ShoppingCart',
        component: purchaseShoppingCart
      },
      {
        path: 'Order',
        name: 'Purchase_Order',
        component: purchaseOrder
      },
      {
        path: 'OrderDetail',
        name: 'Purchase_OrderDetail',
        component: purchaseOrderDetail
      },
      {
        path: 'DetailList',
        name: 'Purchase_DetailList',
        component: purchaseDetailList
      }]
    },
    {
      path: '/Order',
      name: 'Order',
      component: building,
      children: [{
        path: 'OrderList',
        name: 'Order_OrderList',
        component: orderOrderList
      },
      {
        path: 'OrderDetail',
        name: 'Order_OrderDetail',
        component: orderOrderDetail
      }]
    },
    {
      path: '/Service',
      name: 'Service',
      component: service,
      children: [{
        path: 'Policy',
        name: 'Service_Policy',
        component: servicePolicy
      },
      {
        path: 'PolicyDetail',
        name: 'Service_PolicyDetail',
        component: servicePolicyDetail
      },
      {
        path: 'Customer',
        name: 'Service_Customer',
        component: building
      },
      {
        path: 'CustomerDetail',
        name: 'Service_CustomerDetail',
        component: serviceCustomerDetail
      },
      {
        path: 'College',
        name: 'Service_College',
        component: serviceCollege
      },
      {
        path: 'CollegeDetail',
        name: 'Service_CollegeDetail',
        component: serviceCollegeDetail
      },
      {
        path: 'Employee',
        name: 'Service_Employee',
        component: building
      },
      {
        path: 'Shop',
        name: 'Service_Shop',
        component: building
      },
      {
        path: 'Goods',
        name: 'Service_Goods',
        component: building
      }
      ]
    },
    {
      path: '/Pay',
      name: 'Pay',
      component: pay,
      children: [{
        path: 'Wallet',
        name: 'Pay_Wallet',
        component: payWallet,
        children: [{
          path: 'MyWallet',
          name: 'Pay_Wallet_MyWallet',
          component: payWalletMyWallet
        },
        {
          path: 'Recharge',
          name: 'Pay_Wallet_Recharge',
          component: payWalletRecharge
        },
        {
          path: 'RechargePay',
          name: 'Pay_Wallet_RechargePay',
          component: payWalletRechargePay
        },
        {
          path: 'WaterCash',
          name: 'Pay_Wallet_WaterCash',
          component: payWalletMyaccount
        },
        {
          path: 'Balance',
          name: 'Pay_Wallet_Balance',
          component: payWalletMyaccount
        }]
      },
      {
        path: 'PayType',
        name: 'Pay_PayType',
        component: payPayType
      },
      {
        path: 'PayList',
        name: 'Pay_PayList',
        component: payPayList
      },
      {
        path: 'PayFinish',
        name: 'Pay_PayFinish',
        component: payPayFinish
      }]
    },
    {
      path: 'News',
      name: 'News',
      component: news,
      children: [{
        path: 'NewsList',
        name: 'News_NewsList',
        component: serviceCollege
      }]
    },
    {
      path: 'Address',
      name: 'Address',
      component: address,
      children: [{
        path: 'AddressList',
        name: 'Address_AddressList',
        component: addressAddressList
      },
      {
        path: 'AddressEdit',
        name: 'Address_AddressEdit',
        component: addressAddressEdit
      }]
    },
    {
      path: 'distribution',
      name: 'Distribution',
      component: distribution,
      children: [{
        path: 'DistributionList',
        name: 'Distribution_DistributionList',
        component: distributionDistributionList
      }]
    }]
  }]
})

