<template>
  <div >
    <mt-header title="水电宝" v-if="$store.state.common.userId !== ''">
      <div  slot="left">
        <mt-button  v-if="$route.name !== 'Index'" icon="back" @click="$router.go(-1)">返回</mt-button>
        <router-link  :to="{name: 'Pay_Wallet_MyWallet'}"  slot="right" style="color: white">
          <mt-button v-if="$route.name === 'Index'"  >我 的</mt-button>
        </router-link>
        <router-link  :to="{name: 'Address_AddressList'}"  slot="right" style="color: white">
          <mt-button v-if="$route.name === 'Index'"  >地址</mt-button>
        </router-link>
      </div>
      <router-link :to="{name: 'News_NewsList'}"  slot="right">
        <mt-button icon="more" ></mt-button>
      </router-link>
    </mt-header>
    <!--动画层-->
    <!--<transition :name="transitionName">-->
      <router-view class="child-view"></router-view>
    <!--</transition>-->
  </div>

</template>



<script>

export default {

  data () {
    return {
      transitionName: 'slide-left'
    }
  },
  beforeRouteUpdate (to, from, next) {
//    返回按钮做兼容
//    console.log(from)
//    console.log(to)
    if (to.name === 'Index') {
      this.$router.isBack = true
    }
      // 如果isBack为true时，证明是用户点击了回退，执行slide-right动画
    let isBack = this.$router.isBack
    if (isBack) {
      this.transitionName = 'slide-right'
//      console.log('slide-right')
    } else {
      this.transitionName = 'slide-left'
//      console.log('slide-left')
    }
   // 做完回退动画后，要设置成前进动画，否则下次打开页面动画将还是回退
    this.$router.isBack = false
    next()
  },
  created () {
    document.title = this.$store.state.common.title
  }
}
</script>

<style scoped>
.child-view {
    position: absolute;
    width:100%;
  height: 100%;
    transition: all 0.3s cubic-bezier(.6,0,.1,1);
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(450px, 0);
  transform: translate(450px, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-450px, 0);
  transform: translate(-450px, 0);
}

</style>



