<template>
    <div>
    <div class="main" >
        <div class="item_name">
            {{ data.store_name }} {{data.buyer_name}}
            <div class="item-status">
                状态：
                <span v-if="data.order_state === '10'">未付款</span>
                <span v-if="data.order_state === '20'">待配送</span>
                <span v-if="data.order_state === '30'">配送中</span>
                <span v-if="data.order_state === '40'">已收货</span>
            </div>
        </div>
        <div class="item_main">
            <div class="item_one" v-for="item in data.extend_order_goods">
                <div class="item_img" :style="{'background': 'url('+ item.goods_image +')'}"></div>
                <div class="item_info">
                    <span>
                        {{ item.goods_name }}
                    </span>
                    <span>
                       {{ item.goods_price }}
                    </span>
                </div>
            </div>
        </div>
        <div class="item_footer">
            <div>
                共{{ data.extend_order_goods.length }}件商品     需付款{{ data.goods_amount }}元
            </div>
            <div>
                <input v-if="data.order_state === '10' " type="button" value="取消订单" @click="cancel(data.order_sn)">
                <input v-if="data.order_state === '10' " type="button" value="去支付" @click="gopay(data.pay_sn)">
                <router-link :to="{name: 'Purchase_OrderDetail', query: {oid: data.order_id}}" v-if="data.order_state !== '10' "><input  type="button" value="查看订单"></router-link>
                <input v-if="data.order_state === '20' " type="button" value="催单" @click="cuidan">
                <input v-if="data.order_state === '30' " type="button" value="催单" @click="cuidan">
                <input v-if="data.order_state === '30' " type="button" value="确认收货" @click="recive(data.order_sn)">
            </div>
        </div>
    </div>
    </div>

</template>

<script>

  export default {
    props: ['data'],
    data () {
      return {
        result: this.counter
      }
    },
    methods: {
      cuidan: function () {
        this.$toast('已催单，请耐心等待')
      },
      gopay: function (paysn) {
        this.$router.push({name: 'Pay_PayType', query: {pay_sn: paysn}})
      },
      cancel: function (paysn) {
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserOrderAbrogate' +
                '&osn=' + paysn +
                this.$store.state.common.userId
        ).then(function (data) {
          if (data.body.code === '1000') {
            this.$emit('change', 'cancel')
            this.$toast(data.body.msg)
          }
        }, function (req) {
          this.$toast(this.$store.state.common.tip.nointnet)
        })
      },
      recive: function (osn) {
        console.log(osn)
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserOrderReceive' +
                '&osn=' + osn +
                this.$store.state.common.userId
        ).then(function (data) {
          this.$toast(data.body.msg)
          this.$emit('change', 'recive')
        }, function (req) {
          this.$toast(this.$store.state.common.tip.nointnet)
        })
      }
    },
    created: function () {
    }
  }
</script>
<style scoped>
    .main{
        margin-bottom: 0.5rem;
    }
    .item_name{
        height: 2.5rem;
        line-height: 2.5rem;
        background: 3rem;
        padding: 0 1rem;
        padding-right: 8rem;
        position: relative;
        background: white;
    }
    .item-status{
        position: absolute;
        right: 1rem;
        bottom: 0;
        /*color:#ff6633 ;*/
    }
    .item_main{
        background: #F7F7F7;
        min-height: 6rem;
        position: relative;
        /*background: white;*/
    }
    .item_img{
        height: 4rem;
        width: 4rem;
        background: red;
        position: absolute;
        left: 1rem;
        top: 1rem;
        background-size: 100%!important;
        background-repeat: no-repeat!important;
        background-position: center!important;
    }
    .item_one{
        height: 4rem;
        padding: 1rem 0;
        /*box-sizing: border-box;*/
        position: relative;
    }
    .item_info{
        width: 100%;
        height: 100%;
        padding-left: 6rem;
    }
    .item_info span:nth-child(1){
        margin-bottom: 0.3rem;
        display: block;
    }
    .item_info span:nth-child(2){
        font-size: 0.8rem;
    }
    .item_footer{
        text-align: right;
        background: white;
    }
    .item_footer div:nth-child(1){
        height: 2.5rem;
        line-height: 2.5rem;
        padding: 0 1rem;
        color:#666;
        font-size: 0.8rem;
        border-bottom: 0.1rem solid #ccc;
    }
    .item_footer div:nth-child(2){
        height: 2.5rem;
        line-height: 2.5rem;
        padding: 0 1rem;
    }
    .item_footer div:nth-child(2) input{
        border:0.1rem solid #999;
        border-radius: 0.3rem;
        background: white;

    }
</style>



