<template>
  <div class="main">
    <div class="item" v-for="item in list" @click="chooseOne(item)">
      <div class="top">
        {{ item.true_name }}
        <div class="right">{{ item.mob_phone }}</div>
      </div>
      <div class="middle">
        {{ item.area_info }} {{ item.address }}
      </div>
      <div class="bottom">
        <label>
          <My-Radio class="radio" v-bind:data="{radioVal: item.address_id}" v-model="radioMod" @change="changeDefault"></My-Radio>设为默认地址
        </label>
        <div class="right">
          <!--<div class="button">删除</div>-->
          <My-Button val="删除" @click="del(item)"></My-Button>
          <My-Button val="编辑" @click="edit(item)"></My-Button>
        </div>
      </div>
    </div>
    <div class="new" @click="add">新  增</div>
  </div>
</template>


<script>
  import MyButton from '../../common/MyButton'
  import MyRadio from '../../common/MyRadio'
  export default {
    data: function () {
      return {
        list: [],
        radioMod: ''
      }
    },
    components: { MyButton, MyRadio },
    created: function () {
//      this.$store.commit('common_setTitle', '地址列表')
      this.getdata()
    },
    methods: {
      edit: function (item) {
        this.$router.push({name: 'Address_AddressEdit', query: {from: 'edit', address_id: item.address_id}})
      },
      add: function () {
        this.$router.push({name: 'Address_AddressEdit', query: {from: 'new'}})
      },
      defaultAddress: function () {
        for (var i = 0; i < this.list.length; i++) {
          if (this.list[i].is_default === '1') {
//            console.log(this.list[i].address_id)
            this.radioMod = this.list[i].address_id
          }
        }
      },
      changeDefault: function (val) {
        var _this = this
        this.radioMod = val
        this.$messagebox.confirm('您确定要修改默认地址？').then(action => {
          this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserAddressDefault' +
                   '&addressId=' + this.radioMod +
                   this.$store.state.common.userId
          ).then(function (data) {
          }, function (req) {
            this.$toast(this.$store.state.common.tip.nointnet)
          })
        }, function () {
          _this.defaultAddress()
        })
      },
      del: function (item) {
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserAddressDel' +
                '&aid=' + item.address_id +
                this.$store.state.common.userId
        ).then(function (data) {
          this.$router.go()
        }, function (req) {
          this.$toast(this.$store.state.common.tip.nointnet)
        })
      },
      getdata: function () {
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserAddressList' + this.$store.state.common.userId).then(function (data) {
          this.list = data.body.data
          //      默认地址
          this.defaultAddress()
        }, function (req) {
          this.$toast(this.$store.state.common.tip.nointnet)
        })
      },
      chooseOne: function (item) {
        this.$emit('change', item)
      }
    },
    watch: {
    }
  }
</script>
<style scoped>
  .main{
    padding-bottom: 4rem;
  }
  .item{
    margin-top: 1rem;
    background: white;
  }
  .item:active{
    opacity: 0.7;
  }
  .item .top{
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
    padding-right: 8rem;
    font-weight: 600;
    color: #333;
  }
  .item .top .right{
    display: inline-block;
    position: absolute;
    right: 1rem;
  }
  .item .middle{
    padding:0rem 1rem;
    font-size: 0.8rem;
  }
  .item .bottom{
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
    border-top: 1px solid #ccc;
    margin-top: 0.8rem;
  }
  .item .bottom .right{
    display: inline-block;
    position: absolute;
    right: 1rem;
  }
  .new{
    height: 3rem;
    line-height: 3rem;
    width: 100%;
    position: fixed;
    bottom:0;
    background: #F15F25;
    text-align: center;
    font-size: 1.2rem;
    color: white;
  }
  .radio{
    position: relative;
    top: .5rem;
    margin-right: .5rem;
  }
</style>



