<template>
    <div id="main">
        <div class="box">
            <div class="item" v-for="item in data">
                <div class="item_img" v-bind:style="{'backgroundImage':'url(' + item.goods_image+ ')'}"></div>
                <div class="item_main">
                    <div class="item_name">
                        {{ item.goods_name }}
                    </div>
                    <span class="item_price">¥ {{ item.goods_price }}</span>
                    <span class="item_num">X {{ item.goods_paynum }}</span>
                </div>
            </div>
        </div>
    </div>


</template>

<script>
  export default {
    data: function () {
      return {
        data: []
      }
    },
    created: function () {
//      console.log(typeof this.$route.query.data === 'object')
      if (typeof this.$route.query.data === 'object') {
        this.data = this.$route.query.data
      }
    }
  }
</script>

<style scoped>
    .box{
        border:0
    }
    .item{
        height: 6rem;
        background: white;
        border-bottom: 1px solid #ccc;
        position: relative;
    }
    .item_img{
        height: 4rem;
        width: 4rem;
        background: red;
        position: absolute;
        left: 1rem;
        top:1rem;
        background-size: 100%;
        background-repeat: no-repeat;
    }
    .item_main {
        height: 100%;
        padding: 1rem 4rem 1rem 6rem;
        box-sizing: border-box;
    }
    .item_name{
        height: 2.5rem;
        line-height: 2.5rem;
    }
    .item_price{
        color: #666;
    }
    .item_num{
        position: absolute;
        right: 1rem;
        top:2.5rem;
        color:#999;
    }
</style>



