<template>
  <div class="main">
    <label>
      <i class="icon" :class="{checked: radioVal === currentValue}"></i>
      <input type="radio" :value="radioVal" v-model="currentValue" @change="change">
    </label>
  </div>
</template>


<script>
  export default {
    props: {
      data: {
        radioVal: String
      },
      value: String
    },
    data: function () {
      return {
        radioVal: this.data.radioVal,
        currentValue: this.value
      }
    },
    created: function () {
//      console.log(this.value)
    },
    methods: {
      change: function () {
        this.$emit('change', this.radioVal)
      }
    },
    watch: {
      value (val) {
        this.currentValue = val
        console.log(val + ':::::' + this.currentValue)
      },
      currentValue: function (val) {
        this.$emit('input', val)
      }
    }
  }
</script>
<style scoped>
  .main{
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    position: relative;
  }
  label{
    display: inline-block;
  }
  .icon{
    display: inline-block;
    height: 1.5rem;
    width: 1.5rem;
    background: url(../image/icon/checkbox_off.png);
    background-size: 100%;
    position: absolute;
    left: 0 ;
    top: 0;

  }
  .checked{
    background-image: url(../image/icon/checkbox_on.png);
  }
  input[type=radio]{
    display: none;
  }
</style>



