const Index = {
  state: {
    count: 0
  },
  mutations:
  {
    increment: function (state) {
      // state 是模块本地的状态。
      state.count++
    }
  },
  getters: {
    doubleCount: function (state) {
      return state.count * 2
    }
  }
}

module.exports = Index



