<template>
  <div class="main">
    <div class="title">
    订单信息
    </div>
    <div class="section1">
      <div class="store_name">
        {{ data.store_name }}
      </div>
      <div class="item" v-for="(item, index) in data.goods_list"  :key="index">
        <span class="item_name">{{ item.goods_name }}</span>
        <span class="item_num">x {{ item.goods_num }}</span>
        <span class="item_price">¥ {{ item.goods_pay_price }}</span>
      </div>
      <div class="bottom">
        {{ data.payment_name }}
        <span class="right">
          实付  <i>¥ {{ data.goods_amount }}</i>
        </span>
      </div>
    </div>
    <div class="section1_5" v-if="data.order_state === '30' || data.order_state === '40'">
      <div class="title">
        送货信息
      </div>
      <div class="info">
        <div class="main">
          <div class="left">
            <span><i>{{ data.daddress_info.seller_name }}</i><i>{{ data.daddress_info.telphone }}</i></span>
            <!--<span>地址</span>-->
          </div>
          <!--<div class="right">-->
            <!--拨打电话-->
          <!--</div>-->
        </div>
      </div>
    </div>
    <div class="section2">
      <div class="title">
        配送信息
      </div>
      <div class="info">
        <div class="main">
          <div class="left">
            <span><i>{{ data.extend_order_common.reciver_name }}</i><i>{{ data.extend_order_common.reciver_info.phone }}</i></span>
            <span>{{ data.extend_order_common.reciver_info.address }}</span>
          </div>
          <!--<div class="right">-->
            <!--拨打电话-->
          <!--</div>-->
        </div>
      </div>
    </div>

    <div class="section3">
      <div class="title">
        订单基本信息
      </div>
      <div class="main">
        <div class="item_top">
          订单编号： <i>23131454132</i>
          <span class="item_status"> 状态
            <i v-if="data.order_state === '10'">未支付</i>
            <i v-if="data.order_state === '20'">待配送</i>
            <i v-if="data.order_state === '30'">配送中</i>
            <i v-if="data.order_state === '40'">已收货</i>
          </span>
        </div>
        <div class="item">
          <span>成交时间： <i>{{data.add_time}}</i></span>
        </div>
        <div class="item">
          <span>付款时间： <i>{{data.payment_time}}</i></span>
        </div>
        <div class="item" v-if="data.order_state === '30' || data.order_state === '40'">
          <span>配送时间： <i>{{data.extend_order_common.shipping_time}}</i></span>
        </div>
        <div class="item">
          <span>完成时间： <i>{{ data.finnshed_time }}</i></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {
        data: {}
      }
    },
    created: function () {
      this.$store.commit('common_setTitle', '进货单详情')
      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserGoodsOrderInfo' +
              '&oid=' + this.$route.query.oid +
              this.$store.state.common.userId
      ).then(function (data) {
        console.log(data.body.data.order_info)
        this.data = data.body.data.order_info
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
    },
    methods: {}
  }
</script>
<style scoped>
  .main{
    color: #666
  }
  .section{

  }
  .title{
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
  }
  .section1{
    background: white;
  }
  .section1 .store_name{
    height: 2.5rem;
    line-height: 2.5rem;
    color: #666;
    padding: 0 1rem;
    border-bottom: 1px solid #ccc;
    margin-bottom: 0.5rem;
  }
  .item{
    min-height: 2rem;
    line-height: 2rem;
    padding: 0 1rem;
  }

  .item .item_name{
    display: inline-block;
    width: 60%;
    float: left;
  }
  .item .item_num{
    display: inline-block;
    width: 20%;
    text-align: center;
    float: left;
  }
  .item .item_price{
    display: inline-block;
    width: 20%;
    text-align: right;
    float: right;
  }
  .section1 .bottom{
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
    position: relative;
    border-top: 1px solid #ccc;
    margin-top: 0.5rem;
  }
  .section1 .bottom .right{
    position: absolute;
    right:1rem;
    color: #666;
  }
  .section1 .bottom .right i{
    color: red;
    margin-left: 0.2rem;
  }
  .section1_5 .main {
    /*height:  3rem;*/
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
    background: white;
  }
  .section1_5 .left span i{
    display: inline-block;
    /*width: 100%;*/
    height: 2rem;
    line-height: 2rem;
    margin-right: 3rem;
  }
  .section2 .main{
    min-height: 4rem;
    background: white;
    padding: 0.5rem 1rem;
    /*padding-right: 6rem;*/
    position: relative;
  }
  .section2 .left{

  }
  .section2 .left span{
    display: inline-block;
    width: 100%;
    height: 2rem;
    line-height: 2rem;
  }
  .section2 .left span i{
    display: inline-block;
    margin-right: 3rem;
  }
  .section2 .right{
    position: absolute;
    right: 1rem;
    top: 2rem;
    height: 2rem;
    line-height: 2rem;
    width: 5rem;
    text-align: center;
    border: 1px solid #ccc;
    border-radius:0.3rem ;
    font-size: 0.8rem;
  }
  .section2 .right:active{
    background: #F15F25;
    color: white;
    border: 0;
  }
  .section3 .main{
    background: white;
    padding: .5rem 0;
  }
  .section3 .item_top{
    height: 2rem;
    line-height: 2rem;
    padding: 0 1rem;
  }
  .section3 .item_top i{
    color: #999;
  }
  .item_status{
    position: absolute;
    right: 1rem;
  }
  .section3  .item{
    height: 2rem;
    line-height: 2rem;
    padding: 0 1rem;
  }
  .section3  .item i{
    color: #999;
  }
</style>




