<template>
  <div class="main">
    <div class="top">
      <img src="../../image/icon/pay_finish.png" alt="">
      <div class="msg">您已支付成功</div>
    </div>
    <div class="bottom">
      <div class="item">
        <span>订单号：</span>
        <i v-for="item in order_sn">{{ item }}</i>
      </div>
      <div class="item">
        <span>支付方式：</span>
        <i>{{ payType }}</i>
      </div>
      <div class="item">
        <span>支付费用：</span>
        <i>{{ this.$route.query.pay_fee }} 元</i>
      </div>
      <div class="check" @click="check"> 确 认</div>
    </div>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {
//        payType: ''
        order_sn: this.$route.query.order_sn
      }
    },
    created: function () {
      this.$store.commit('common_setTitle', '支付完成')
    },
    methods: {
      check: function () {
        this.$router.replace({name: 'Purchase_Order'})
      }
    },
    computed: {
      payType: function () {
        if (this.$route.query.payType === 'watercash') {
          return '水币支付'
        } else if (this.$route.query.payType === 'remain') {
          return '余额支付'
        } else if (this.$router.query.payType === 'weixin') {
          return '微信支付'
        } else if (this.$router.query.payType === 'zhifubao') {
          return '支付宝支付'
        }
      }
    }
  }
</script>
<style scoped>
  .main{
    background: white;
    padding-bottom: 0.5rem;
  }
  .top{
    text-align: center;
    padding-top: 3rem;
  }
  .top img{
    width: 5rem;
  }
  .top .msg{
    height: 4rem;
    line-height: 4rem;
    color: #999;
    padding-bottom: 1rem;
    font-size: 1.4rem;
    border-bottom: 1px solid #ccc;
  }
  .bottom{
    padding: 1rem;
  }
  .bottom .item{
    mint-height: 2.5rem;
    line-height: 2.5rem;
  }
  .bottom .item i{
    display: block;
    color: #999;
    padding-left: 5.5rem;
  }
  .bottom .item span{
    position: absolute;
    left: 1rem;
    width: 5rem;
  }
  .check{
    background: #F15F25;
    height: 3rem;
    text-align: center;
    line-height: 3rem;
    color: white;
    border-radius: 0.3rem;
    margin-top: 2rem;
    font-size: 1.2rem;
  }
  .check:active{
    opacity: 0.7;
  }
</style>




