<template>
    <div id="main">
        <ul
                v-infinite-scroll="loadMore"
                infinite-scroll-disabled="loading"
                infinite-scroll-distance="10">
            <li >
            <div class="shop_box" v-for="(shop ,shop_index) in list_shop">
                <div class="shop_name">
                    <label class="shop_checkbox">
                        <input type="checkbox"  class=""  v-model="shop_checkboxVal" v-bind:value="'shop' + shop_index " @change="shop_checkboxChange(shop_index)">
                    </label>

                    <img src="../../image/icon/shop_icon.png" alt="" class="icon">
                    {{ shop[0].store_name }}{{ shop_index }}
                </div>
                <div class="shop_item" v-for="(item, item_index) in shop">
                    <!--滑动条部分-->
                    <mt-cell-swipe
                        title=""
                        :right="[
                        {
                          content: '删除',
                          style: { background: '#F15F25', color: 'white', 'line-height': '7rem'},
                          handler: deleteItem(item)
                        }
                      ]">
                        <input type="checkbox" class="item_checkbox" v-model="item_checkboxVal" :value="Number(item.goods_id)" @change="item_checkboxChange(shop_index)" >
                        <div class="item_img" v-bind:style="{'backgroundImage':'url(' + host + '/data/upload/shop/store/goods/' + item.store_id + '/' + item.goods_image+')'}"></div>
                        <div class="item_main">
                            <div class="item_name">
                                {{ item.goods_name }}
                            </div>
                            <div class="item_bottom">
                                <div class="item_info">
                                    <!--<span>规格</span> <br />-->
                                    ¥  <span>{{ item.goods_price }}</span>
                                </div>
                                <div class="item_number" @click="addShoppingCart({shop_index: shop_index ,item_index: item_index, item})">
                                    <number-Inpur v-bind:data="{counter: item.goods_num, min: item.goods_distorage, max: item.goods_storage}" ref="item_inputVal" ></number-Inpur>
                                </div>
                            </div>
                        </div>
                        <div class="zhanwei"></div>

                    </mt-cell-swipe>
                    <!--<div class="item_favourable">-->
                        <!--<span>我是优惠信息</span>-->
                    <!--</div>-->
                </div>


            </div>
        </li>
        </ul>
        <div class="pay">
            <div class="pay_text">
                合计：¥ <span>{{ total }}</span>
            </div>

            <div class="pay_submit" @click="goPayList">
                结算（{{ itemNumber }}）
            </div>
        </div>
    </div>
</template>

<script>
  import numberInpur from '../../common/NumberInput'
  export default {
    data: function () {
      return {
        shop_checkboxVal: [],
        item_checkboxVal: [],
        list_item: [],
        list_shop: [],
        host: this.$store.state.common.host,
        PayQuery: { test: '123' },
        total: 0
      }
    },
    beforeMount: function () {
    },
    components: { numberInpur },
    methods: {
      addShoppingCart: function (data) {
//        计算当前input 位置
        var nowindex = 0
        for (var i = 0; i < data.shop_index; i++) {
          nowindex += Number(this.list_shop[i].length)
        }
        nowindex += Number(data.item_index)
//        获得更改的INPUT的值
        var inputVal = this.$refs.item_inputVal[nowindex].result

//        请求更改购物车数量
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getGoodsCartPlus&cart_id=' + data.item.cart_id + '&quantity=' + inputVal + this.$store.state.common.userId).then(
          function (res) {
            // 处理成功的结果
            if (res.body.code === '1000') {
//              this.$toast({
//                message: res.body.msg,
//                iconClass: 'd-icon-success'
//              })
            }
            this.computeTotal()
          },
          function (res) {
            // 处理失败的结果
            this.$toast('加入购物车失败')
          }
        )
      },
      loadMore: function () {
        console.log('more')
        this.loading = true
        setTimeout(function () {
//          let last = this.list[this.list.length - 1]
//          for (let i = 1; i <= 10; i++) {
//              this.list.push(last + i)
//          }
//          this.loading = false
        }, 2500)
      },
      shop_checkboxChange: function (val) {
        for (var i = 0; i < this.list_shop[val].length; i++) {
          for (var j = 0; j < this.item_checkboxVal.length; j++) {
            if (this.item_checkboxVal[j] === this.list_shop[val][i].goods_id) {
              this.item_checkboxVal.splice(j, 1)
//                console.log()
            }
          }
        }
        for (var x = 0; x < this.shop_checkboxVal.length; x++) {
          if ('shop' + x === 'shop' + val) {
            console.log('shop' + val)
            for (var y = 0; y < this.list_shop[x].length; y++) {
              this.item_checkboxVal.push(this.list_shop[x][y].goods_id)
            }
          }
        }
        this.computeTotal()
      },
      item_checkboxChange: function (val) {
        if (this.item_checkboxVal.length === 0) {
          this.shop_checkboxVal = []
        } else {
          for (var i = 0; i < this.list_shop[val].length; i++) {
            for (var j = 0; j < this.item_checkboxVal.length; j++) {
              if (this.list_shop[val][i].goods_id === this.item_checkboxVal[j]) {
                for (var k = 0; k < this.shop_checkboxVal.length; k++) {
                  if (this.shop_checkboxVal[k] === ('shop' + val)) {
                    this.shop_checkboxVal.splice(k, 1)
                  }
                }
              }
            }
          }
          this.shop_checkboxVal.push('shop' + val)
        }
        this.computeTotal()
      },
      deleteItem: function (data) {
        var _this = this
        return function () {
          console.log(data.cart_id)
          _this.$http.get(_this.$store.state.common.host + '/index.php?act=mobileApi&op=getGoodsCartDel&cart_id=' + data.cart_id + _this.$store.state.common.userId).then(
            function (res) {
                // 处理成功的结果
              console.log(res.body)
              if (res.body.code === '1000') {
                this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getGoodsCartList' + this.$store.state.common.userId).then(function (data) {
                  this.list_shop = data.body.data || []
                }, function (req) {
                  this.$toast(this.$store.state.common.tip.nointnet)
                })
              }
            },
            function (res) {
            // 处理失败的结果
              _this.$toast('删除购物车失败')
            }
          )
        }
      },
      goPayList: function () {
//        console.log(this.$refs.item_inputVal)
        var _this = this
        var pushQuery = []
        for (var i = 0; i < this.list_shop.length; i++) {
          for (var j = 0; j < this.list_shop[i].length; j++) {
            for (var k = 0; k < this.item_checkboxVal.length; k++) {
//              console.log(this.list_shop)
              if (this.list_shop[i][j].goods_id === this.item_checkboxVal[k]) {
                pushQuery.push(this.list_shop[i][j].cart_id)
              }
            }
          }
        }
        if (pushQuery.length < 1) {
          _this.$toast('请至少选择一件商品')
          return false
        }
//        页面跳转
        function routerPush (pushData) {
          _this.$router.push({name: 'Pay_PayList', query: {data: pushQuery, from: 'cart'}})
        }
        routerPush(pushQuery)
      },
      computeTotal: function () {
        var total = 0
        for (var i = 0; i < this.list_shop.length; i++) {
          for (var j = 0; j < this.list_shop[i].length; j++) {
            for (var k = 0; k < this.item_checkboxVal.length; k++) {
              if (this.item_checkboxVal[k] === this.list_shop[i][j].goods_id) {
//              计算当前input 位置
                var nowindex = 0
                for (var x = 0; x < i; x++) {
                  nowindex += this.list_shop[x].length
                }
                nowindex += j
                var inputVal = this.$refs.item_inputVal[nowindex].result
                total += parseFloat(this.list_shop[i][j].goods_price) * inputVal
              }
            }
          }
        }
        this.total = Math.round(total * 100) / 100
      }
    },
    created: function () {
      this.$store.commit('common_setTitle', '购物车')
      var _this = this
      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getGoodsCartList' + this.$store.state.common.userId).then(function (data) {
        this.list_shop = data.body.data || []
        for (var i = 0; i < _this.list_shop.length; i++) {
          this.shop_checkboxVal.push('shop' + i)
          for (var j = 0; j < _this.list_shop[i].length; j++) {
            _this.list_shop[i][j].goods_id = Number(_this.list_shop[i][j].goods_id)
            _this.item_checkboxVal.push(Number(_this.list_shop[i][j].goods_id))
          }
        }
        setTimeout(function () {
          _this.computeTotal()
        }, 1000)
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
    },
    computed: {
      itemNumber: function () {
        return this.item_checkboxVal.length
      }
    }
  }
</script>

<style scoped>

    .mint-cell-left{
        padding-top:1rem!important ;
        background: red;
    }
    .mint-cell-swipe-button{
        line-height: 8rem!important;
    }

    #main{
        padding-bottom: 3.5em;
        height: 100%;
        box-sizing: border-box;
    }
    .shop_box{
        height: 100%;
    }
    .shop_checkbox{
        display: inline-block;
    }
    .shop_name{
        height: 3rem;
        line-height: 3rem;
        padding: 0 1rem;
        background: #F5F5F5;
    }
    .shop_name .icon{
        height: 1.5rem;
        position: relative;
        top: 0.2rem;
        margin-right: 0.5rem;
        margin-left: 0.3rem;
    }
    .shop_item{
        border-bottom: 0.4rem solid #f0f0f0;
    }
    .item_img{
        width: 5rem;
        height: 5rem;
        position: absolute;
        left: 3rem;
        top: 1rem;
        z-index: 1;
        background-size: 100%;
        background-repeat: no-repeat;
    }
    .zhanwei{
        height: 7rem;
    }
    .item_main{
        width: 100%;
        position: absolute;
        left: 0;
        top: 0;
        padding: 1rem;
        padding-left: 8.5rem;
        background: #FAFAFA;
        box-sizing: border-box;
    }

    .item_checkbox{
        position: absolute;
        left: 1rem;
        z-index: 1;
        top: 3rem;
    }
    .item_name{
        color:#666;
        height: 1.5rem;
        font-size: 1rem;
        line-height: 1.5rem;
        margin-top: 0.8rem;
        box-sizing: border-box;
    }
    .item_bottom{
        height: 3.5rem;
        /*background: red;*/
        width: 100%;
        position: relative;
    }
    .item_info{
        padding-right: 8.5rem;
    }
    .item_info span:nth-child(1){
        font-size: 1rem;
        margin: 0.8rem 0;
        display:inline-block ;
    }
    .item_info span:nth-child(3){
        font-size: 1rem;
    }
    .item_number{
        position: absolute;
        right: 0rem;
        top: 0.5rem;
    }
    .item_favourable{
        height: 2.5rem;
        line-height: 2.5rem;
        font-size: 0.8rem;
        padding: 0 1rem;
        color: #999;
    }
    .pay{
        position: fixed;
        bottom: 0;
        left: 0;
        height: 3.5rem;
        width: 100%;
        background: white;
        padding: 0 1rem;
        box-sizing: border-box;
        padding-right: 5rem;
        border-top: 0.1rem solid #f0f0f0;
    }
    .pay_text{
        height: 100%;
        float: left;
        line-height: 3.5rem;
        font-size: 1.2rem;
    }
    .pay_submit{
        position: absolute;
        right: 0;
        line-height: 3.5rem;
        font-size: 1.2rem;
        width: 7.5rem;
        text-align: center;
        background: #F15F25;
        color: white;

    }
</style>



