

<template>
    <div id="main">
        <div class="top" v-for="top_item in top">
            <div class="top_img" v-bind:style="{'backgroundImage':'url('+top_item.goodsImg+')'}"></div>
            <div class="top_mian">
                <div class="top_name">{{ top_item.goodsName }}</div>
                <div class="top_price">¥<span>{{ top_item.goodsPrice }}</span></div>
                <div class="top_number" >
                    <input type="button" value="数量" class="icon" >
                    <div class="right">
                        <number-Inpur v-bind:data="{counter: top_item.goodsDiStorage, max: top_item.goodsStorage, min: top_item.goodsDiStorage}"
                            ref="top_inputVal"></number-Inpur>
                    </div>

                </div>
                <!--<router-link :to="{name: 'Pay_PayList',query: {data: top.goodsId, quantity: topNum, from: 'one'}}">-->
                    <button class="top_buy" @click="buyone">立即购买</button>
                <!--</router-link>-->
            </div>
        </div>
        <ul
                v-infinite-scroll="loadMore"
                infinite-scroll-disabled="loading"
                infinite-scroll-distance="10" class="item_box">
            <li v-for="(item, index) in list" class="item">
                <!--{{ item }}-->
                <div class="item_img" v-bind:style="{'backgroundImage':'url('+item.goodsImg+')'}">

                </div>
                <div class="item_main">
                    <div class="item_name">{{ item.goodsName }}</div>
                    <div class="item_spec">剩余：{{ item.goodsStorage}}</div>
                    <div class="item_price">¥ {{ item.goodsPrice }}</div>
                    <div class="item_shop">{{ item.storeName }}</div>
                </div>
                <div class="right" >
                    <div class="item_number">
                        <number-Inpur v-bind:data="{counter: item.goodsDiStorage, max: item.goodsStorage, min: item.goodsDiStorage}" ref="item_inputVal"></number-Inpur>

                    </div>
                    <div class="item_shoppingcart" @click="addShoppingCart(item.goodsId,index)" >
                        <img class="icon" src="../../image/icon/shopping_cart.png" alt="">
                        加入购物车
                    </div>

                </div>
            </li>
        </ul>
        <div class="bottom">
            <router-link :to="{name: 'Purchase_Index'}">
                <div class="bottom_item">
                    <img src="../../image/icon/purchase_index.png" alt="">
                    首页
                </div>
            </router-link>
            <router-link :to="{name: 'Purchase_ShoppingCart'}">
                <div class="bottom_item">
                    <img src="../../image/icon/purchase_shoppingcart.png" alt="">
                    <i class="tip" v-if="shoppingCart_sum !== 0"><mt-badge type="error" size="small">{{ shoppingCart_sum }}</mt-badge></i>
                    购物车
                </div>
            </router-link>
            <router-link :to="{name: 'Purchase_Order'}">
                <div class="bottom_item">
                    <img src="../../image/icon/purchase_order.png" alt="">
                    进货单
                </div>
            </router-link>

        </div>
    </div>

</template>



<script >
  import numberInpur from '../../common/NumberInput'
  export default {
    data: function () {
      return {
        list: [],
        top: [],
        shoppingCart_sum: 0,
        topNum: 0
      }
    },
    components: {numberInpur},
    beforecreate: function () {

    },
    created: function () {
      this.$indicator.open()

//      设置TITLE
      this.$store.commit('common_setTitle', '采购')
//        请求页面数据
      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserSeeGoodsList&fcy=2&' + this.$store.state.common.userId).then(
        function (res) {
                // 处理成功的结果
          if (res.body.code === '1000') {
            this.list = res.body.data.slice(1)
            this.top[0] = res.body.data[0]
            console.log(this.top)
          } else {
            this.$toast(res.body.msg)
          }
          this.$indicator.close()
        },
        function (res) {
          this.$toast(this.$store.state.common.tip.nointnet)
          this.$indicator.close()
        }
      )
//        查询购物车数量
      this.getShoppingcartSum()
    },
    beforeMount: function () {

//      alert('123')
    },
    methods: {
      loadMore: function () {
        console.log('more')
        this.loading = true
        setTimeout(function () {
//        let last = this.list[this.list.length - 1]
//        for (let i = 1; i <= 10; i++) {
//          this.list.push(last + i)
//        }
          this.loading = false
        }, 2500)
      },
      addShoppingCart: function (goodsId, index) {
        var inputVal = this.$refs.item_inputVal[index].result
        if (inputVal < 1) {
          this.$toast('请至少添加一件商品')
          return false
        }
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getGoodsCart&goodsId=' + goodsId + '&payNum=' + inputVal + '&' + this.$store.state.common.userId).then(
          function (res) {
              // 处理成功的结果
            console.log(res.body)
            if (res.body.code === '1000') {
              this.$toast({
                message: '已加入购物车'
              })
              this.getShoppingcartSum()
            } else {
              this.$toast(res.body.msg)
            }
          },
          function (res) {
                  // 处理失败的结果
            this.$toast('加入购物车失败')
          }
        )
      },
      buyone: function () {
        if (this.$refs.top_inputVal.result < 1) {
          this.$toast('请至少添加一件商品')
          return false
        }
        this.$router.push({name: 'Pay_PayList', query: {goods_id: this.top[0].goodsId, quantity: this.$refs.top_inputVal[0].result, from: 'one'}})
      },
      getShoppingcartSum: function () {
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getGoodsCartCount' + this.$store.state.common.userId).then(
          function (data) {
          // 处理成功的结果
            if (data.body.code === '1000') {
              console.log(data.body)
              this.shoppingCart_sum = data.body.data
            } else {
              this.$toast(data.body.msg)
            }
          },
          function (data) {
            this.$toast(this.$store.state.common.tip.nointnet)
          }
        )
      }
    }
  }
</script>
<style scoped>
    #main{
        padding-bottom: 4.2rem;
        background: white;
    }
    .top{
        height: 12rem;
        /*background: red;*/
        border-bottom: 0.6rem solid #F5F5F5;
        padding: 1rem 0;
        box-sizing: border-box;
    }
    .top_img{
        height: 9.5rem;
        width: 9.5rem;
        background: #f0f0f0;
        position: absolute;
        left: 1rem;
        top: 1rem;
        background-size:100% ;
        background-repeat:no-repeat ;
    }
    .top_mian{
        width: 100%;
        height: 100%;
        /*background: red;*/
        padding-left: 12rem;
        padding-right: 1rem;
        box-sizing: border-box;
    }
    .top_name{
        height: 1.8rem;
        line-height: 1.8rem;
        font-size: 1.2rem;
        font-weight: 500;
    }
    .top_price{
        color: red;
        font-size: 1.1rem;
        height: 1.8rem;
        line-height: 1.8rem;

    }
    .top_number{
        height: 2.6rem;
        width: 100%;
        margin-top:0.5rem ;
        margin-bottom: 0.2rem;
        position: relative;
    }
    .top_number .right{
        float: right;
        transform: scale(1.1);
        position: absolute;
        right: 0.3rem;
        top: 0;
    }
    .top_number .icon{
        background: white;
        border: 1px solid #ccc;
        border-radius: 0.2rem;
        height: 1.5rem;
        margin-right: 1rem;
        box-sizing: border-box;
        width: 3.5rem;
        color: #666;
        text-align: center;
        font-size: 10px;
        position: absolute;
        left: 0;
        top: 0rem;
        transform: scale(1.1);
        color: #999;
    }
    .top_buy{
        background: #F15F25;
        border:0;
        color:white;
        height: 2.2rem;
        width: 100%;
        border-radius: 0.3rem;
    }
    .item_box{
        padding:0 1rem;
        list-style: none;
        margin: 0;
    }
    .item{
        height: 8rem;
        border-bottom: 0.1rem solid #E6E6E6;
        position: relative;
    }
    .item_img{
        height: 6rem;
        width: 6rem;
        /*background: red;*/
        position: absolute;
        top: 1rem;
        background-size:100% ;
        background-repeat:no-repeat ;
    }
    .item_main{
        /*background: yellow;*/
        width: 100%;
        height: 100%;
        padding-left: 6.5rem;
        padding-right: 7rem;
        box-sizing: border-box;
        padding-top:1.2rem;
        padding-bottom: 1rem;
    }
    .item_name{
        height: 1.5rem;
        line-height: 1.5rem;
        font-size: 1.1rem;
    }
    .item_spec{
        font-size: 0.8rem;
        height: 1.8rem;
        line-height: 1.8rem;
        color:#666;

    }
    .item_price{
        font-size: 1rem;
        height: 1.5rem;
        line-height: 1.5rem;
        color:red;
    }
    .item_shop{
        font-size: 0.8rem;
        color:#999;
    }
    .item .right{
        height: 100%;
        width: 7rem;
        position: absolute;
        right: 0rem;
        top:0em;
        padding: 1rem 0;
        box-sizing: border-box;
        text-align: right;
    }
    .item_number{

    }
    .item_shoppingcart{
        width: 6.7rem;
        height: 1.8rem;
        line-height: 2rem;
        text-align: center;
        position: absolute;
        bottom:1rem;
        border: 1px solid #ccc;
        border-radius: 0.2rem;
        background: white;
        font-size: 0.7rem;
        color: #666;
        position: ;
        right: 0;
    }

    .item_shoppingcart:active{
        background: #ccc;
        color: white;
    }
    .item_shoppingcart .icon{
        height: 1rem;
        position: relative;
        top: 0.2rem;
    }
    .bottom{
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 3.5rem;
        background: #F5F5F5;
        font-size: 0.8rem;
    }
    .bottom img{
        height: 1.3rem;
        display: block;
        margin: 0 auto;
        margin-top: 0.8rem;
        margin-bottom: 0.1rem;
    }
    .bottom .bottom_item{
        width: 33.3%;
        display: inline-block;
        float: left;
        text-align: center;
        position: relative;
    }
    .bottom a:nth-child(1){
        color:#F28E35
    }
    .bottom .bottom_item .tip{
        position: absolute;
        margin-left: .5rem;
        display: inline-block;
        left: 50%;
        top: 0.2rem;
    }
</style>



