<template>
  <div class="main">
    <router-link :to="{name: 'Pay_Wallet_WaterCash'}">
      <div class="item item1">
        <img class="icon" src="../../../image/icon/wallet_watercash.png" alt="">
        <span>水币</span>
        <span class="right">{{ data.water_fee }}</span>
      </div>
    </router-link>
    <!--<div class="item">-->
      <!--<img class="icon" src="../../../image/icon/wallet_coupon.png" alt="">-->
      <!--<span>优惠券</span>-->
      <!--<span class="right">2000</span>-->
    <!--</div>-->
    <router-link :to="{name: 'Pay_Wallet_Balance'}">
      <div class="item">
        <img class="icon" src="../../../image/icon/wallet_balance.png" alt="">
        <span>余额</span>
        <span class="right">{{ data.available_predeposit }}</span>
      </div>
    </router-link>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {
        data: ''
      }
    },
    created: function () {
      this.$store.commit('common_setTitle', '我的钱包')
      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserInfo' + this.$store.state.common.userId).then(function (data) {
//        console.log(data.body.data)
        this.data = data.body.data
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
    },
    methods: {}
  }
</script>
<style scoped>
  .main{

    padding-top: .8rem;
  }
  .item1{
    border-top: 1px solid #ccc;
  }
  .item{
    height: 3rem;
    line-height: 3rem;
    background: white;
    padding: 0 1rem;
    color: #666;
    border-bottom: 1px solid #ccc;
  }
  .item .icon{
    height: 1.5rem;
    position: relative;
    top: .4rem;
    margin-right: 0.5rem;
  }
  .item .right{
    float: right;
  }
</style>




