// import Transition from './components/PageTransition'
<template>
  <div id="app">
  	<router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  created () {
//      登陆检测
    if (!this.$store.state.userInfo) {
//      alert('您尚未登陆')
    }
//        从app获取用户
    if (this.$store.state.common.userId === '') {
//      var testData = JSON.stringify({'token': '5a5e2a944c111ca96261e83ae293f52f', 'mobile': '13713956265'})
      var androidData = window.android.getUser()
      var data = JSON.parse(androidData)
      var user = '&mobile=' + data.mobile + '&token=' + data.token
      this.$store.commit('setUser', user)
    }
//    title设置
    document.title = this.$store.state.common.title
  }
}
</script>

<style>
    #app {
      font-family: 'Avenir', Helvetica, Arial, sans-serif;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      font-size:14px;
      /*height: 100%;*/

    }
    i{
      font-style: normal ;
    }
    html,body{
        font-size:14px;
        background: #f5f5f5;
        /*height: 100%;*/
        color:#666
    }
    ul{
        margin: 0;
        padding: 0;
    }
    li{
        list-style: none;
    }
    a{
        color: #333;
        text-decoration: none;
    }
    /*css 右箭头 》 */
    .arrow-box{
        width:1rem;
        height:2rem;
        /*margin:20px auto;*/
        position:relative;
        margin: 0;
        display: inline-block;
    }
    .arrow_right{
        /*position: relative;*/
    }

    .right-arrow1,.right-arrow2{
        width:0;
        height:0;
        display:block;
        position:absolute;
        left:0;
        top:0;
        border-top:10px transparent dashed;
        border-right:10px transparent dashed;
        border-bottom:10px transparent dashed;
        border-left:10px white solid;
        overflow:hidden;
    }
    .right-arrow1{
        left:1px;/*重要*/
        border-left:10px #999 solid;
    }
    .right-arrow2{
        border-left:10px white solid;
    }

    /*苹果按钮兼容*/
    input[type="button"], input[type="submit"], input[type="reset"] {

        -webkit-appearance: none;

    }
    textarea {  -webkit-appearance: none;}


    /*苹果五兼容*/
    @media screen and (max-width:321px) {
        html {
            font-size: 10px;
        }
    }



</style>



