<template>
  <div class="main">
    <mt-navbar v-model="selected">
      <mt-tab-item id="1">全部</mt-tab-item>
      <mt-tab-item id="2">未支付</mt-tab-item>
      <mt-tab-item id="3">已支付</mt-tab-item>
      <mt-tab-item id="4">配送中</mt-tab-item>
      <mt-tab-item id="5">已收货</mt-tab-item>
    </mt-navbar>
    <div class="box">


    <!-- tab-container -->
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
          <router-link :to="{name: 'Order_OrderDetail'}">
            <Order-Item></Order-Item>
          </router-link>
        </mt-tab-container-item>
        <mt-tab-container-item id="2">
        2
        </mt-tab-container-item>
        <mt-tab-container-item id="3">
         3
        </mt-tab-container-item>
        <mt-tab-container-item id="4">
        4
        </mt-tab-container-item>
        <mt-tab-container-item id="5">
         5
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>


<script>
  import OrderItem from './OrderItem'
  export default {
    data: function () {
      return {
        selected: '1'
      }
    },
    components: {OrderItem},
    created: function () {
      this.$store.commit('common_setTitle', '订单管理')
    },
    methods: {}
  }
</script>
<style scoped>
  .box{
    border-top: 0.5rem solid #EBEBEB;
  }
  .mint-navbar .mint-tab-item.is-selected{
    color: #FC963B!important;
    border-bottom:3px solid #FC963B!important;
  }
</style>



