<template>
  <div class="main">
    <div class="section1">
      <div class="item title">
        订单号：{{ pdr_sn }}
      </div>
      <div class="item">
        充值金额
        <span class="right">
          ¥  {{ money }}
        </span>
      </div>
    </div>
    <div class="section2">
      <div class="item title">
        选择支付方式
      </div>
      <div class="item">
        余额支付
        <span class="right">
          <My-Radio class="radio"  v-bind:data="{radioVal: 'Banlace'}"  v-model="value">123</My-Radio>
        </span>
      </div>
      <div class="item">
        微信支付
        <span class="right">
          <My-Radio class="radio"  v-bind:data="{radioVal: 'Wechat'}"  v-model="value">123</My-Radio>
        </span>
      </div>
    </div>

  </div>
</template>


<script>
  import MyRadio from '../../../common/MyRadio'
  export default {
    data: function () {
      return {
        value: '',
        pdr_sn: this.$route.query.pdr_sn,
        money: this.$route.query.money
      }
    },
    components: {MyRadio},
    created: function () {
      this.$store.commit('common_setTitle', 'demo')
    },
    methods: {},
    watch: {
      value: function () {
//        console.log(this.value)
      }
    }

  }
</script>
<style scoped>
  .section1 {
    margin: 0.5rem 0;
  }
  .title{
    color: #999;
  }
  .item{
    height: 3rem;
    line-height: 3rem;
    background: white;
    padding: 0 1rem;
    border-bottom: 1px solid #ccc;
  }
  .item:nth-child(1){
    border-top: 1px solid #ccc;
  }
  .item .right{
    float: right;
    height: 100%;
    box-sizing: border-box;

  }
  .item .right .radio{
    margin-top: 0.8rem;
  }
</style>



