<template>
  <div class="main">
    <div class="top">
      <div>
        充值金额
      </div>
      <input type="tel" placeholder="请输入充值水币数量" v-model="money" maxlength="6">
    </div>
    <router-link :to="{name: 'Pay_Wallet_RechargePay', query: {money: item.money}}"  v-for="item in list">
      <div class="item">
        <span>冲{{ item.criterion }}</span>
        <span class="right"> 送{{ item.append }}</span>
      </div>
    </router-link>
    <div class="submit" @click="submit">充    值</div>
  </div>
</template>


<script>
  export default {
    data: function () {
      return {
        list: [
//        {money: 2000, give: 200},
//        {money: 3000, give: 300},
//        {money: 5000, give: 500},
//        {money: 10000, give: 800}
        ],
        money: ''
      }
    },
    created: function () {
      this.$store.commit('common_setTitle', 'demo')
      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserWatercoinActivityList' + this.$store.state.common.userId).then(function (data) {
        console.log(data.body.data)
        this.list = data.body.data
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
    },
    methods: {
      submit: function () {
//        console.log(this.money)
        this.toPay(this.money)
      },
      toPay: function (money, give) {
        give = give || 0
        if (give < 0) {
          this.$toast('请输入正确的金额')
          return false
        }
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserWatercoinAdd' +
                '&amount=' + money +
                '&append=' + give +
                this.$store.state.common.userId
        ).then(function (data) {
          this.$router.push({name: 'Pay_Wallet_RechargePay', query: {pdr_sn: data.body.data.pdr_sn, money: data.body.data.pdr_amount}})
        }, function (req) {
          this.$toast(this.$store.state.common.tip.nointnet)
        })
      }
    },
    watch: {
      money: function (val) {
        this.money = Math.round(Number(val))
      }
    }
  }
</script>
<style scoped>
  .top{
    margin-bottom: 1rem;
  }
  .top >div{
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
  }
  .top input{
    width: 100%;
    height: 3rem;
    line-height: 3rem;
    padding: 0 1rem;
    border: 0;
    box-sizing: border-box;
  }
  .item{
    height: 3rem;
    line-height: 3rem;
    background: white;
    padding: 0 1rem;
    padding-right: 6rem;
    margin-bottom: 1rem;
    border-top:1px solid #ccc;
    border-bottom:1px solid #ccc;
  }
  .item:active{
    background: #F8E5DE;
  }
  .item .right{
    position: absolute;
    right: 1rem;
    color: #999;
  }
  .submit{
    width: 90%;
    text-align: center;
    height: 3rem;
    line-height: 3rem;
    background:#F15F25 ;
    margin: 0 auto;
    color: white;
    font-size: 1.2rem;
    border-radius: 0.5rem;
    margin-top: 3rem;
  }
  .submit:active{
    opacity: 0.7;
  }
</style>




