<template>

    <div class="button"  @click="handleClick">{{ val }}</div>

</template>


<script>
  export default {
    props: ['val'],
    data: function () {
      return {

      }
    },
    created: function () {

    },
    methods: {
      handleClick: function (evt) {
        this.$emit('click', evt)
      }
    },
    watch: {

    }
  }
</script>
<style scoped>
  .button{
    display: inline-block;
    border: 1px solid #999;
    padding: 0 1rem;
    height: 1.6rem;
    line-height: 1.6rem;
    border-radius: 0.3rem;
    color: #333;
  }
  .button:active{
    border: 1px solid #F15F25;
    color:#F15F25;
  }
</style>



