
<template>
    <div class="main">
        <router-link :to="{name: 'Service_CollegeDetail', query: { pid: item.id, from: 'college'}}" v-for="item in data">
            <div class="item"  v-if="item.play !== '1'" :class="{checked: item.see === '1'}">
                <div class="item_title">{{ item.title }}</div>
                <div class="item_text">{{ item.intro }}</div>
                <div class="arrow-right arrow-box right">
                    <b class="arrow_right"><i class="right-arrow1"></i><i class="right-arrow2"></i></b>
                </div>
            </div>
        </router-link>
    </div>
</template>



<script>
  export default {
    data: function () {
      return {
        data: [],
        selected: this.$route.query.active || '1',
        list: [1, 2]
      }
    },
    created: function () {
      var _this = this
      var url = ''

      if (this.$route.name === 'Service_College') {
        this.$store.commit('common_setTitle', '水来学院')
        url = '/index.php?act=mobileApi&op=getUserSchoolList'
      } else if (this.$route.name === 'News_NewsList') {
        this.$store.commit('common_setTitle', '消息列表')
        url = '/index.php?act=mobileApi&op=getUserMessageList'
      }
      _this.$http.get(_this.$store.state.common.host + url + _this.$store.state.common.userId).then(function (data) {
        if (data.body.code === '1000') {
          this.data = data.body.data
        } else {
          _this.$toast(data.body.msg)
        }
        console.log(data.body.data)
      }, function (req) {
        _this.$toast(_this.$store.state.common.tip.nointnet)
      })
    }
  }
</script>
<style scoped>
    .box{
        border-top: 0.5rem solid #EBEBEB;
    }
    .mint-navbar .mint-tab-item.is-selected{
        color: #FC963B!important;
        border-bottom:3px solid #FC963B!important;
    }
    .top_line{
        background-color:#f0f0f0;
        width: 1px;
        height: 1.5rem;
        display: inline-block;
        position: absolute;
        right: 50%;
        top: 1rem;
    }
    .item{
        height: 3.5rem;
        line-height: 3.5rem;
        background: white;
        border-bottom: 1px solid #E6E6E6;
        padding: 0rem 1rem;
        padding-right: 4rem;
        position: relative;
        box-sizing: border-box;
    }

    .item_title{
        /*height: 3rem;*/
        /*line-height: 3rem;*/
    }
    .item_text{
        height: 1.5rem;
        line-height: 1.5rem;
        font-size: 0.8rem;
        color: #666;
    }
    .item .right{
        position: absolute;
        right: 1rem;
        top: 1rem;
        color: #999;
        transform: scale(0.8);
    }
    .item:active{
        background: #EDF9FE;
    }
    .checked{
        background: #EDF9FE;
    }
</style>



