<template>
    <div id="main">
        <div class="address" @click="popupAddress = true">
            <div class="top" v-if="is_address">
                <span>{{ data.address_info.true_name }}</span>  &nbsp;  &nbsp;
                <span>{{ data.address_info.mob_phone }}</span>
            </div>
            <div class="bottom" v-if="is_address">
                <img class="icon" src="../../image/icon/address.png" alt="">
                <span>{{ data.address_info.area_info }} {{ data.address_info.address }}
                </span>
            </div>
            <div class="noaddress" v-if="!is_address">
                <img class="icon" src="../../image/icon/address.png" alt="">
                您还没有收货地址，请点击这里设置
            </div>
        </div>
        <div class="images" @click="ToDetailList">
            <div class="images_box" >
                <div class="image_item" v-for="item in list" v-bind:style="{'backgroundImage':'url(' + item.goods_image+ ')'}">
                    <!--{{ item.goods_image }}-->
                </div>
            </div>
            <div class="images_info">
                <i v-if="count > 4">....</i>&nbsp&nbsp共
                <span> {{ count }}</span>个
                <div class="arrow-right arrow-box arrow">
                    <b class="arrow_right"><i class="right-arrow1"></i><i class="right-arrow2"></i></b>
                </div>
            </div>

        </div>
        <div class="paytype">
            <div class="left" @click="popStatus = true">
                支付方式
                <span v-if="paytype === 'online' ">在线支付</span>
                <span v-if="paytype === 'offline' ">货到付款</span>
            </div>

        </div>
        <div class="paynumber">
            <div class="left">
                商品金额
                <span>{{ total }}</span>
            </div>
        </div>
        <div class="nav">
            <div class="left">
                实付款：
                <span>¥&nbsp;{{ total }}</span>
            </div>
            <div class="right" @click="submit">提交订单</div>
        </div>

        <mt-popup v-model="popStatus" position="right" class="poppage" :modal="false" >
            <div class="paytype_pop">
                <!--<mt-radio-->
                        <!--align="right"-->
                        <!--title=""-->
                        <!--v-model="paytype"-->
                        <!--:options="options" >-->
                <!--</mt-radio>-->

                    <label for="online">
                        <div class="paytype_pop_item" @click="closepop" >
                            <span>在线支付</span>
                            <i class="radio" :class="{checked: paytype === 'online'}" ></i>
                        </div>
                    </label>
                    <label for="offline">
                        <div class="paytype_pop_item" @click="closepop" >
                            <span>货到付款</span>
                            <i class="radio" :class="{checked: paytype === 'offline'}" ></i>
                        </div>
                    </label>
                 <input id="online" v-model="paytype" type="radio" value="online">
                 <input id="offline" v-model="paytype" type="radio" value="offline">
            </div>

        </mt-popup>

        <mt-popup
                v-model="popupAddress"
                position="right"
                style="height: 100%;width: 100%;background: #f0f0f0">
            <div class="address_pop">
                <address-Tmpl @change="chooseAddress"></address-Tmpl>
            </div>
        </mt-popup>
    </div>

</template>

<script>
  import addressTmpl from '../address/AddressList'
  export default {
    data: function () {
      return {
        data: {
          address_info: {
            true_name: ''
          },
          cart_list: []
        },
        list: [],
        popStatus: false,
        paytype: 'online',
        popupAddress: false,
        is_address: true
      }
    },
    components: { addressTmpl },
    created: function () {
        //      设置TITLE
      this.$store.commit('common_setTitle', '提交订单')
      var _this = this
      var from = this.$route.query.from
      _this.$http.get(_this.$store.state.common.host + '/index.php?act=mobileApi&op=getGoodsCartPayStep1' +
        '&from=' + from +
        '&PayData=' + this.$route.query.data +
        '&goods_id=' + this.$route.query.goods_id +
        '&quantity=' + this.$route.query.quantity +
        _this.$store.state.common.userId
      ).then(function (data) {
        if (data.body.code === '1000') {
          this.data = data.body.data
          this.list = data.body.data.cart_list
          if (data.body.data.address_info.length === 0) {
            this.is_address = false
          }
          console.log(data.body.data)
        } else if (data.body.code === '2000') {
          this.$router.go(-1)
        }
      }, function (req) {
        _this.$toast(req.body.msg)
      })
    },
    methods: {
      ToDetailList: function () {
        this.$router.push({name: 'Purchase_DetailList', query: {data: this.list}})
      },
      closepop: function () {
        this.popStatus = false
      },
      submit: function () {
        this.$indicator.open()
        var sendData = {
          'PayData': this.$route.query.data,
          'from': 'cart',
          'payName': this.paytype,
          'cityId': this.data.address_info.city_id,
          'addressId': this.data.address_info.address_id
        }
        if (this.$route.query.from === 'one') {
          sendData.from = 'one'
          sendData.goods_id = this.list[0].goods_id
          sendData.quantity = this.list[0].goods_paynum
        }
        this.$http.post(this.$store.state.common.host + '/index.php?act=mobileApi&op=postGoodsCartPayStep2' + this.$store.state.common.userId, sendData).then(function (data) {
          if (data.body.code === '1000') {
            this.$indicator.close()
            if (this.paytype === 'offline') {
              this.$toast(data.body.msg)
              this.$router.push({name: 'Purchase_Order'})
            } else if (this.paytype === 'online') {
              this.$router.push({name: 'Pay_PayType', query: {pay_sn: data.body.data.pay_sn}})
            }
          }
        }, function (req) {
          this.$indicator.close()
          this.$toast(req.body.msg)
        })
      },
      chooseAddress: function (add) {
        this.popupAddress = false
        this.data.address_info = add
      }
    },
    computed: {
      count: function () {
        return this.list.length
      },
      total: function () {
        var result = 0
        for (var i = 0; i < this.list.length; i++) {
          result += Number(this.list[i].goods_price) * Number(this.list[i].goods_paynum)
        }
        return Math.round(result * 100) / 100
      }
    }
  }
</script>

<style scoped>
    body{
        background: #f2f2f2;
    }
    #main{
        /*position: absolute;*/
        /*left: 0;*/
        /*top: 0;*/
        /*height: 100%;*/
        /*width: 100%;*/
        padding-bottom: 4rem;
    }
    .address{
        min-height: 5rem;
        margin-bottom: .5rem ;
        background: white;
    }
    .address .icon{
        width: 1.2rem;
        position: absolute;
        left: 1rem;
        top: 2.3rem;
    }
    .address .top{
        padding-left:3rem ;
        height: 2.5rem;
        line-height: 3rem;

    }
    .address .bottom{
        min-height: 2rem;
        padding-left: 3rem;
        font-size: 0.8rem;
        padding-right: 4rem;
        color:#666
    }
    .images{
        height: 6rem;
        background: white;
        position: relative;
        border-bottom:0.1rem solid #ccc;
    }
    .images_box{
        width: 100%;
        height: 100%;
        /*background: red;*/
        box-sizing: border-box;
        padding: 1rem;
        padding-right: 8rem;
        overflow: hidden;
    }
    .images_info{
        width: 8rem;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;
    }
    .image_item{
        width: 4rem;
        height: 4rem;
        margin-right: 0.1rem;
        margin-bottom: 1rem;
        background: yellow;
        display: inline-block;
        background-size: 100%;
        background-repeat: no-repeat;
    }
    .images_info{
        padding:0 1rem;
        line-height: 6rem;
        text-align: right;
        box-sizing: border-box;
        font-size: 0.8rem;
        color: #999;
    }
    .paytype,.paynumber{
        height: 3rem;
        line-height: 3rem;
        background: white;
        border-bottom:0.1rem solid #ccc;
        padding: 0 1rem;
    }
    .paytype span,.paynumber span{
        float: right;
    }
    .paytype span{
        color: #999;
    }
    .paynumber span{
        color: #FC2D2D
    }
    .nav{
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 3.5rem;
        /*background: red;*/
    }
    .nav .left{
        width: 50%;
        height: 100%;
        background: white;
        display: inline-block;
        line-height: 4rem;
        padding-left: 1rem;
        box-sizing: border-box;
        font-size: 1.2rem;
    }
    .nav .left span{
        color:red
    }
    .nav .right{
        width: 50%;
        height: 100%;
        float: right;
        background: #F15F25;
        display: inline-block;
        line-height: 4rem;
        text-align: center;
        font-size: 1.2rem;
        color: white;
    }
    .poppage{
        height: 100%;
        width: 100%;
    }
    .arrow{
        position: relative;
        top: 0.9rem;
        transform: scale(0.8);
        margin-left: 0.5rem;
    }
    .paytype_pop{
        padding: .5rem 0;

    }
    .paytype_pop .paytype_pop_item{
        display:block;
        height: 3rem;
        line-height: 3rem;
        border-bottom:1px solid #ccc ;
        padding: 0 1rem;
        position: relative;
    }
     input[type='radio']{
        opacity: 0;
    }
    .paytype_pop .radio{
        float: right;
        display: inline-block;
        height: 1.5rem;
        width: 1.5rem;
        background: url("../../image/icon/checkbox_off.png");
        position: absolute;
        right: 1rem;
        top: 0.5rem;
        background-size: 100%;
        position: relative;
    }
    .paytype_pop .checked{
        background-image: url("../../image/icon/checkbox_on.png");
    }
    .noaddress{
        padding-left: 3rem;
        /*text-align: center;*/
        line-height:5rem ;
    }
    .noaddress .icon {
        top: 1.7rem;
    }
</style>



