<template>
  <div class="main">
    <div class="title">
      <h1>{{ data.title }}</h1>
      <span>发布时间{{ data.addtime }}</span>
    </div>
    <div class="box">

      <div class="text" v-html="data.content">
      </div>
    </div>
  </div>
</template>



<script>
  export default {
    data: function () {
      return {
        data: {}
      }
    },
    created: function () {
      if (this.$route.query.from === 'college') {
        this.$store.commit('common_setTitle', '水来学院')
      } else if (this.$route.query.from === 'new') {
        this.$store.commit('common_setTitle', '消息')
      }

      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserSchoolInfo&pid=' + this.$route.query.pid + this.$store.state.common.userId).then(function (data) {
        if (data.body.code === '1000') {
          this.data = data.body.data
        } else {
          this.$toast(data.body.msg)
        }
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
    },
    methods: {}
  }
</script>
<style scoped>
  .main{
    height: 100%;
    width: 100%;
    background: white;
    padding-bottom: 3.2rem;
    box-sizing: border-box;
    padding-top: 0.5rem;
  }
  .title{
    text-align: center;
  }
  .title h1{
    font-size: 1.2rem;
    font-weight: 500;
    margin:0;
    padding: 0;
    height: 2.5rem;
    line-height: 2.5rem;
    /*margin-top: 0.5rem;*/
    color: #333;
  }
  .title span{
    color: #666;
    display: inline-block;
    width: 90%;
    margin: 0 auto;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid #ccc;

  }
  .text span{
    display: block;
    height: 2.5rem;
    line-height: 2.5rem;
    color: #666;
  }
  .box{
    padding: 1rem;

  }
  .box .time{
    /*height: 1.5rem;*/
    /*line-height: 1.5rem;*/
    color: #999;
    font-size: 0.8rem;
    position: relative;
    top:-0.5rem;
  }

</style>



