<template>
  <div class="main">
    <div class="top">
      456465
      <span class="right">状态：已支付</span>
    </div>
    <div class="userinfo">
      <div class="top">
        <span>名字</span>
        <span>13554895222</span>
        <span>第 <i>3</i> 次下单</span>
      </div>
      <div class="bottom">
        我是地址
      </div>
    </div>
    <div class="goods">
      <div class="title">
        商品
      </div>
      <div class="goods_item">
        <span class="name">阿尔卑斯矿泉水</span>
        <span class="num">x5</span>
        <span class="price">¥888</span>
      </div>
      <div class="goods_item sum">
        <span class="name"></span>
        <span class="num">共1件商品</span>
        <span class="price">¥8888</span>
      </div>
    </div>
    <div class="orderinfo">
      <div class="top">订单编号：10000000</div>
      <div class="bottom">订单时间：2012.12.12
        <div class="right">
          <My-Button val="配送"></My-Button>
          <My-Button val="取消订单"></My-Button>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
  import MyButton from '../../common/MyButton'
  export default {
    data: function () {
      return {}
    },
    components: {MyButton},
    created: function () {
      this.$store.commit('common_setTitle', 'demo')
    },
    methods: {}
  }
</script>
<style scoped>
  .main {
    border-bottom: 1px solid #ccc;
    margin-bottom: 1rem;
  }
  .main >.top{
    min-height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
    padding-right: 8rem;
    background: white;
  }
  .main .top .right{
    position: absolute;
    right: 1rem;
  }
  .userinfo{
    background: #F5F5F5;
    padding: 0.5rem 0;
  }
  .userinfo .top{
    height: 2rem;
    line-height: 2rem;
    padding: 0 1rem;
  }
  .userinfo .top span{
    display: inline-block;
    margin-right: 1rem;
  }
  .userinfo .top span:nth-child(3) i{
    color: #F15F25;
  }
  .userinfo .bottom{
    height: 2rem;
    line-height: 2rem;
    padding: 0 1rem;
  }
  .goods{
    background: white;
    border-bottom: 1px solid #ccc;
  }
  .goods .title{
    line-height: 2.5rem;
    padding: 0 1rem;
    border-bottom: 1px solid #ccc;
    margin-bottom: 0.5rem;
  }
  .goods .goods_item{
    box-sizing: border-box;
    min-height: 2rem;
    line-height: 2rem;
    padding: 0 1rem;
  }
  .goods .goods_item >span{
    display: inline-block;
  }
  .goods .goods_item >span:nth-child(1){
    width: 50%;
  }
  .goods .goods_item >span:nth-child(2){
    width: 30%;
  }
  .goods .goods_item >span:nth-child(3){
    width: 19%;
    position: absolute;
    right:0rem;
    text-align: right;
    padding-right: 1rem;
  }
  .goods .sum{
    padding-bottom: 0.5rem;
  }
  .orderinfo{
    background: white;
   padding: 0.5rem 0;
  }
  .orderinfo .top{
    height: 2rem;
    line-height: 2rem;
    padding: 0 1rem;
  }
  .orderinfo .bottom{
    height: 2rem;
    line-height: 2rem;
    padding: 0 1rem;
    padding-right: 10rem;
    position: relative;
  }
  .orderinfo .bottom .right{
    position: absolute;
    right: 1rem;
    top: 0;
  }
</style>



