<template>
<div>
        <div class="wallet">
            <div class="title item">钱包支付</div>
            <label for="watercash" @click="popupVisible=true">
                <div class="item">
                    <img class="icon" src="../../image/icon/wallet_watercash.png" alt="">水币
                    <div class="right">
                        <span>{{ data.water_fee }}</span>
                        <i class="radio" :class="{checked: radioVal === 'watercash'}" ></i>
                        <input id="watercash" type="radio" v-model="radioVal" value="watercash" @change="redioChange">
                    </div>
                </div>
            </label>
            <label for="ramain" @click="popupVisible=true">
                <div class="item" >
                    <img class="icon" src="../../image/icon/wallet_balance.png" alt="">余额
                    <div class="right">
                        <span>{{ data.available_predeposit }}</span>

                          <i class="radio" :class="{checked: radioVal === 'remain'}" ></i>

                        <input id="ramain" type="radio" v-model="radioVal" value="remain"  @change="redioChange" >
                    </div>

                </div>
            </label>
        </div>

            <div class="online">
                <div class="title item">在线支付</div>
                <!--<label for="zhifubao" >-->
                    <!--<div class="item">-->
                        <!--支付宝-->
                        <!--<div class="right">-->
                            <!--&lt;!&ndash;<span>123</span>&ndash;&gt;-->

                                <!--<i class="radio" :class="{checked: radioVal === 'zhifubao'}" ></i>-->

                            <!--<input id="zhifubao" type="radio" v-model="radioVal" value="zhifubao"  @change="redioChange">-->
                        <!--</div>-->
                    <!--</div>-->
                <!--</label>-->
                <label for="weixin" >
                    <div class="item">
                        <img class="icon" src="../../image/icon/wallet_wechatpay.png" alt="">微信支付
                        <div class="right">
                            <i class="radio" :class="{checked: radioVal === 'weixin' }" ></i>
                            <input id="weixin" type="radio" v-model="radioVal" value="weixin"  @change="redioChange">
                        </div>
                    </div>
                </label>
            </div>


        <mt-popup
                v-model="popupVisible"
                position="bottom"
                style="width:100%">
            <label for="pwd">
            <div class="paycode">
                <div class="title">
                    <i class="close" @click="closepop"></i>
                    <span>钱包支付</span>
                </div>
                <div class="main">
                    <span>向水来了订货平台支付</span> <br />
                    <span class="paymoney">¥ {{ data.pay_fee }}</span> <br />
                    <span class="paytype">支付方式：
                        <i v-if="radioVal === 'watercash' ">水币</i>
                        <i v-if="radioVal === 'remain' ">余额</i>
                    </span> <br />
                    <span class="">支付密码</span>
                    <div class="paypsw">
                        <div class="paypsw_box"><i v-if="pwdlength > 0">●</i></div>
                        <div class="paypsw_box"><i v-if="pwdlength > 1">●</i></div>
                        <div class="paypsw_box"><i v-if="pwdlength > 2">●</i></div>
                        <div class="paypsw_box"><i v-if="pwdlength > 3">●</i></div>
                        <div class="paypsw_box"><i v-if="pwdlength > 4">●</i></div>
                        <div class="paypsw_box"><i v-if="pwdlength > 5">●</i></div>
                    </div>
                </div>
                <div class="submit" @click="submit">确认支付</div>
                <input type="tel" id="pwd" v-model="pwd" v-focus maxlength="6">
            </div>

            </label>

        </mt-popup>
    </div>

</template>

<script>
  export default {
    data: function () {
      return {
        radioVal: '',
        popupVisible: false,
        pwd: '',
        data: ''
      }
    },

    created: function () {
      this.$store.commit('common_setTitle', '支付方式')
      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserInfo' + '&pay_sn=' + this.$route.query.pay_sn + this.$store.state.common.userId).then(function (data) {
        console.log(data.body.data)
        this.data = data.body.data
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
      window.paySuccess = function () {
        alert('hello paysuccess')
      }
      console.log(window)
    },
    methods: {
      redioChange: function () {
        if (this.radioVal === 'watercash' || this.radioVal === 'remain') {
          this.popupVisible = true
        } else if (this.radioVal === 'zhifubao') {
          console.log('zhifubao')
//          ALiPay();
        } else if (this.radioVal === 'weixin') {
          var sendData = JSON.stringify({
            pay_sn: this.$route.query.pay_sn,
            pay_sum: this.data.pay_fee,
            order_sn: this.data.order_sn
          })
          window.android.WeChatPay(sendData)
        }
      },
      closepop: function () {
        this.popupVisible = false
      },
      submit: function () {
        if (this.pwd.length < 6) {
          this.$messagebox('提示', '请输入完整的支付密码')
          return false
        }
        var sendData = {
          payType: '',
          paypwd: this.pwd,
          paySn: this.$route.query.pay_sn
        }
        if (this.radioVal === 'watercash') {
          sendData.payType = 'sbpay'
        } else if (this.radioVal === 'remain') {
          sendData.payType = 'pdpay'
        }
        this.popupVisible = false
        this.$indicator.open()
        this.$http.post(this.$store.state.common.host + '/index.php?act=mobileApi&op=postGoodsCartPayStep3' + this.$store.state.common.userId, sendData).then(function (data) {
          this.$indicator.close()
          if (data.body.code === '1000') {
            this.pwd = ''
            this.$router.push({name: 'Pay_PayFinish', query: {payType: this.radioVal, pay_fee: this.data.pay_fee, order_sn: this.data.order_sn}})
          } else {
            this.$messagebox('提示', data.body.msg)
          }
        }, function (req) {
          this.$indicator.close()
          this.$toast(this.$store.state.common.tip.nointnet)
        })
      }
    },
    watch: {
      pwd: function (val) {
        if (val.length > 6) {
          return val.slice(0, 6)
        }
//        console.log(val)
      },
      popupVisible: function (val) {
        if (!val) {
          this.pwd = ''
        }
      }
    },
    computed: {
      pwdlength: function () {
        return this.pwd.length
      }
    },
    directives: {
      focus: {
          // 指令的定义---
        update: function (el) {
              // 聚焦元素
          el.focus()
        }
      }
    }
  }
</script>

<style scoped>
    .wallet{
        margin-top: 1rem;
    }
    .online{
        margin-top: 1rem;
    }
    .title{
        font-size: 0.8rem;
    }
    .item{
        height:3rem;
        line-height: 3rem;
        background: white;
        padding: 0 1rem;
        color: #333;
        position: relative;
        border-bottom: 1px solid #ccc;
    }
    .item:active{
        opacity: 0.7;
    }
    .item .right{
        position: absolute;
        right: 1rem;
        top: 0;
    }
    .item .right span{
        color: #999;
    }
    .item .radio{
        display: inline-block;
        height: 1.5rem;
        width: 1.5rem;
        background: url("../../image/icon/checkbox_off.png");
        background-size: 100%;
        position: relative;
        top: 0.3rem;
        margin-left: 1rem;
    }
    .item input {
        display: none;
    }
    .item .right .checked{
        background-image: url("../../image/icon/checkbox_on.png");
    }
    .paycode{
        width: 100%;
        background: white;
        position: relative;
    }
    .paycode .title{
        width: 100%;
        height: 2.5rem;
        line-height: 2.5rem;
        text-align: center;
        font-size: 1rem;
        border-bottom: 1px solid #ccc;
    }
    .paycode .close{
        display: inline-block;
        height: 1.2rem;
        width: 1.2rem;
        background: url("../../image/icon/close.png");
        background-size: 100%;
        background-position: center;
        position: absolute;
        left: 1rem;
        top: .6rem;

    }
    .paycode .main{
        text-align: center;
        color: #666;
    }
    .paycode .main span{
        height: 2rem;
        line-height: 2rem;
    }
    .paycode .main .paymoney{
        font-size: 1.6rem;
    }
    .paycode .main .paytype{
        font-size: 0.8rem;
        color: #999;
    }
    .paypsw{
        width: 19rem;
        height: 3rem;
        margin: 0 auto;
        margin-bottom: 1rem;
        box-sizing: border-box;

    }
    .paypsw .paypsw_box{
        display: inline-block;
        height: 3rem;
        line-height: 3rem;
        font-size: 1rem;
        width: 3rem;
        color: #808080;
        /*background: red;*/
        float: left;
        border:.1rem solid #ccc;
        border-left:0;
    }
    .paypsw .paypsw_box:nth-child(1){
        border-left:0.1rem solid #ccc;
    }
    .paycode input[type='tel'] {
        /*margin: 0 auto;*/
        display: inline-block;
        opacity: 0;
        height: 0.5rem;
        position: relative;
        left: -100rem;
        top: 100rem;
    }
    .paycode .submit{
        text-align: center;
        width: 80%;
        background: #F15F25;
        height: 2.5rem;
        line-height: 2.5rem;
        color: white;
        border-radius: 0.3rem;
        margin: 0 auto;
        margin-top: 1.5rem;
    }
    .item .icon{
        width: 1.5rem;
        margin-right: 1rem;
        position: relative;
        top: 0.3rem;
    }

</style>



