<template>
  <div class="main">
    <div class="top">
      <span>
        账户可用{{ type }}
      </span>
      <div class="money">
        <i v-if="type === '水币'">{{data.water_fee}}</i>
        <i v-if="type === '余额'">{{data.available_predeposit}}</i>
      </div>
      <div v-if="type === '水币'" class="button" @click="goRecharge">水币充值</div>
      <div v-if="type === '余额'" class="button">提    现</div>
    </div>

    <div class="detail">
      <div class="title">收支明细</div>
      <div class="item" v-for="item in list">
        <div class="top">{{ item.dece }} <span class="right">{{ item.fee }}</span> </div>
        <div class="middle">{{ item.addtime }}</div>
        <div class="bottom" v-if="item.order_sn">{{ item.order_sn }}</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {
        data: {},
        type: '',
        list: []
      }
    },
    created: function () {
//      Indicator.open({
//        text: '加载中...',
//        spinnerType: 'fading-circle'
//      })
      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserInfo' + this.$store.state.common.userId).then(function (data) {
        this.data = data.body.data
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
      if (this.$route.name === 'Pay_Wallet_WaterCash') {
        this.$store.commit('common_setTitle', '我的水币')
        this.type = '水币'
      } else if (this.$route.name === 'Pay_Wallet_Balance') {
        this.$store.commit('common_setTitle', '我的余额')
        this.type = '余额'
      }
      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserWatercoinLogList' + this.$store.state.common.userId).then(function (data) {
        console.log(data.body.data)
        this.list = this.quickSort(data.body.data).reverse()
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
    },
    methods: {
      goRecharge: function () {
        this.$router.push({name: 'Pay_Wallet_Recharge'})
      },
      quickSort: function (arr) {
//       如果数组<=1,则直接返回
        if (arr.length <= 1) { return arr }
        var pivotIndex = Math.floor(arr.length / 2)
//        找基准，并把基准从原数组删除
        var pivot = arr.splice(pivotIndex, 1)[0]
//        定义左右数组
        var left = []
        var right = []
//       比基准小的放在left，比基准大的放在right
        for (var i = 0; i < arr.length; i++) {
          if (arr[i].time <= pivot.time) {
            left.push(arr[i])
          } else {
            right.push(arr[i])
          }
        }
//      递归
        return this.quickSort(left).concat([pivot], this.quickSort(right))
      }
    }
  }
</script>
<style scoped>
  .main >.top{
    height: 10.5rem;
    background: url('../../../image/pay/background.png') no-repeat;
    background-size: cover;
    background-position: bottom;
    color: white;
    text-align: center;
    padding-top: 2rem;
    box-sizing: border-box;
  }
  .top span:nth-child(1) {
    font-size: 0.8rem;
  }
  .top .money{
    height: 3rem;
    line-height: 3rem;
    font-size: 1.8rem;
  }
  .top .button{
    height: 2rem;
    line-height: 2rem;
    width: 7rem;
    margin: 0 auto;
    border:1px solid white;
    border-radius: 2rem;
    margin-top: .3rem;
  }
  .top .button:active{
    opacity: 0.7;
  }
  .detail{
    background: white;
    border-bottom: 1px solid #ccc;
  }
  .detail .title{
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
    background:#F2F2F2 ;
  }
  .detail .item{
    padding: .5rem 0;
    border-bottom: 1px solid #ccc;
  }
  .detail .item:active{
    opacity: 0.7;
  }
  .detail .top {
    height: 2rem;
    line-height: 2rem;
    padding: 0 1rem;
    padding-right: 5rem;
    font-size: 1.1rem;
  }
  .detail .top .right{
    position: absolute;
    right: 1rem;
    font-size: 1rem;
  }
  .detail .middle, .detail .bottom{
    padding: 0 1rem;
    color: #999;
    height: 1.5rem;
    line-height: 1.5rem;
  }
</style>




