<template>
    <div class="main">
        <div class="title">
            <h1>{{ data.title }}</h1>
            <span v-if="data.play !== '1'">发布时间{{ data.privilege_cerat_time }}</span>
            <span v-if="data.play === '1'">签约时间{{ data.playtime }}</span>
        </div>
        <div class="box">
            <div class="time">
                有效时间： {{ data.privilege_valid_starttime }} --  {{ data.privilege_valid_endtime }}
            </div>
            <div class="text">
                <!--{{ data.privilege_val }}-->
                <span v-for="(item, index) in data.privilege_arr">
                 {{index + 1}} .   累计{{ data.privilege_name }}超过(含等于){{ item.num }},可获得{{ item.per }}%的返利
                </span>
            </div>
        </div>
        <div v-if="data.play !== '1'"   :class="{submit_show: data.play !== '1'}" class="submit" @click="submit">
            立即签约
        </div>
        <div class="animation"  v-if="data.play === '1'">

        </div>
        <mt-popup
                v-model="popupVisible"
                popup-transition="popup-fade">
            <div class="phonecode">
                <div class="title">提&nbsp;&nbsp;示</div>
                <div class="msg">为了签约的有效性，合同签约需要短信验证码</div>
                <div class="code">
                    <input v-model="phonecode" type="tel" placeholder="4位短信验证码">
                    <span @click="getphonecode" >{{ phonecode_text }}</span>
                </div>
                <div class="submit_code" @click="submit_true">
                    确&nbsp;&nbsp;定
                </div>
            </div>
        </mt-popup>
    </div>
</template>



<script>
export default {
  data: function () {
    return {
      data: {
        play: '2'
      },
      popupVisible: false,
      phonecode_text: '发送短信',
      code_statue: true,
      phonecode: ''
    }
  },
  created: function () {
    this.$store.commit('common_setTitle', '政策签约')
    this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserPolicyInfo&pid=' + this.$route.query.pid + this.$store.state.common.userId).then(function (data) {
      if (data.body.code === '1000') {
        this.data = data.body.data
      } else {
        this.$toast(data.body.msg)
      }
      console.log(data.body.data)
    }, function (req) {
      this.$toast(this.$store.state.common.tip.nointnet)
    })
  },
  methods: {
    submit: function () {
//      this.$router.go()
      this.popupVisible = true
//      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserPolicyGo&seller_id=' + this.sellser_id + '&pid=' + this.id + '&pwd=' + pwd + this.$store.state.common.userId).then(function (data) {
//        this.data = data.body.data
//        console.log(data.body.data)
//      }, function (req) {
//        this.$toast(req.body.msg)
//      })
    },
    getphonecode: function () {
      var _this = this
      console.log(this.code_statue)
      var t = 60
      if (this.code_statue) {
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getPolicySns' + this.$store.state.common.userId).then(function (data) {
//          console.log(data.body.data)
        }, function (req) {
          this.$toast(req.body.msg)
        })
//        时间计算
        var time = setInterval(function () {
          if (t > 0) {
            t += -1
            _this.phonecode_text = t + 's后重发'
//            console.log(t)
          } else {
            clearInterval(time)
            _this.phonecode_text = '发送短信'
            _this.code_statue = true
          }
        }, 1000)
      }

      this.code_statue = false
    },
    submit_true: function () {
      console.log(this.phonecode)
      this.popupVisible = false
      this.$indicator.open({
        text: '签约中...'
      })

      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserPolicyGo' + '&seller_id=' + this.data.seller_id + '&pid=' + this.data.id + '&pwd=' + this.phonecode + this.$store.state.common.userId).then(function (data) {
        console.log(data.body.data)
        this.$indicator.close()
        if (data.body.code === '1000') {
          this.$router.go()
        } else {
          this.$toast(data.body.msg)
        }
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
    }
  }
}
</script>
<style scoped>
    .main{
        height: 100%;
        width: 100%;
        background: white;
        padding-bottom: 3.2rem;
        box-sizing: border-box;
        padding-top: 0.5rem;
    }
    .title{
        text-align: center;
    }
    .title h1{
        font-size: 1.2rem;
        font-weight: 500;
        margin:0;
        padding: 0;
        height: 2.5rem;
        line-height: 2.5rem;
        /*margin-top: 0.5rem;*/
        color: #333;
    }
    .title span{
        color: #666;
        display: inline-block;
        width: 90%;
        margin: 0 auto;
        padding-bottom: 0.5rem;
        border-bottom: 1px solid #ccc;

    }
    .text span{
        display: block;
        height: 2.5rem;
        line-height: 2.5rem;
        color: #666;
    }
    .box{
        padding: 1rem;

    }
    .box .time{
        /*height: 1.5rem;*/
        /*line-height: 1.5rem;*/
        color: #999;
        font-size: 0.8rem;
        position: relative;
        top:-0.5rem;
    }
    .submit{
        height: 3rem;
        width: 100%;

        line-height: 3rem;
        text-align: center;
        color: white;
        font-size: 1.3rem;
        position: fixed;
        bottom: -5rem;
        background: white;
        background: #bfbfbf;
    }
    .submit_show{
        animation:submit 0.5s ;
        animation-fill-mode : forwards
    }
    @keyframes submit {
        from {bottom: -5rem}
        to {bottom: 0}
    }
    .submit:active{
        opacity: 0.8;
        background: #F15F25;
        color:white
    }
    .animation{
        position: absolute;
        top: 1rem;
        right: 1rem;
        height: 7rem;
        width: 7rem;
        background: url('../../../image/icon/service_policy.png');
        animation:animate 0.5s ;
        background-size: 100%;
        background-repeat: no-repeat;
        z-index: 0;
        animation-fill-mode : forwards;

    }
    @keyframes animate {
        from {opacity: 0;transform: scale(2)}
        to {opacity: 0.5; transform: scale(1)}
    }
    .phonecode{
        /*height: 16rem;*/
        width: 16rem;
    }
    .phonecode .title{
        height: 3rem;
        width: 100%;
        line-height: 3rem;
        background: #FC963B;
        text-align: center;
        color: white;
    }
    .phonecode .msg{
        font-size: 1rem;
        color: #666;
        padding: 1rem;
        border-bottom: 1px solid #ccc;

    }
    .code{
        padding: 1rem;
        border-bottom: 1px solid #ccc;
    }
    .code input{
        width: 60%;
        border:0;
        outline: none;
    }
    .code span{
        width: 38%;
        display: block;
        float: right;
        color: #666;
        border-left:1px solid #ccc;
        text-align: center;
        padding-left:0.5rem;
        box-sizing: border-box;
    }
    .code span:active{
        opacity: 0.7;
    }
    .phonecode .submit_code{
        height: 3rem;
        line-height: 3rem;
        text-align: center;
    }
    .phonecode .submit_code:active{
        color:#FC963B
    }
</style>



