<template>
    <div class="main">
        <input v-on:click="minus" value="-" type="button" >
        <input v-model="result" type="text"  @change="blur(result)">
        <input v-on:click="add" value="+" type="button">
    </div>
</template>

<script>

export default {
  props: ['data'],
  data () {
    return {
      result: Number(this.data.counter),
      max: Number(this.data.max),
      min: Number(this.data.min)
    }
  },
  created: function () {
    console.log(this.data)
  },
  methods: {
    minus: function () {
      this.result -= 10
    },
    add: function () {
      this.result += 10
    },
    blur: function (result) {
      if (result < this.min) {
        this.result = this.min
      } else if (result > this.max) {
        this.result = this.max
      }
    }
  },
  watch: {
    counter (val) {
      this.result = val
    },
    result (val) {
      val = Number(val)
      if (val < this.min) {
        this.result = this.min
      } else if (val > this.max) {
        this.result = this.max
      }
    }
  }
}
</script>
<style scoped>
    .main{
        color:red;
        font-size: 1rem;
        display: inline-block;
    }
    input[type=text]{
        width: 3.2rem;
        height: 1.5rem;
        line-height: 1.5rem;
        border:1px solid #CCCCCC;
        text-align: center;
        color: #ccc;
        border-radius: 0.2rem;
        padding: 0 0.15rem;
        box-sizing: border-box;
        position: relative;
        top: 0rem;
    }
    input[type=button]{
        border:1px solid #CCCCCC;
        background: white;
        height: 1.5rem;
        width: 1.5rem;
        color:#ccc;
        margin: 0 0em;
        border-radius: 0.2rem;
        box-sizing: border-box;
        padding: 0;
    }
    button:active{
        background:#ccc ;
        color: white;
    }
    input[type=button]:active{
        background: #ccc;
        color: white;
    }

</style>



