<template>
  <div class="main">
    <label>
      <div class="item_input">
        <span class="name">收件人姓名</span>
        <input type="text" placeholder="" v-model="username">
      </div>
    </label>
    <label>
      <div class="item_input">
        <span class="name">联系电话</span>
        <input type="tel" placeholder="" v-model="phone">
      </div>
    </label>
    <label>
      <div class="item_input" @click="popupVisible = true">
        <span class="name">所在地区</span>
        <!--<input type="text" placeholder="" v-model="phone"  >-->
        <span>{{ addressProvince.name }} {{addressCity.name}} {{addressDistrict.name}}</span>
        <div class="right">
          <div class="arrow-right arrow-box arrow">
            <b class="arrow_right"><i class="right-arrow1"></i><i class="right-arrow2"></i></b>
          </div>
        </div>
      </div>
    </label>
    <label>
      <div class="item_input">
        <span class="name">详细地址</span>
        <input type="text" placeholder="" v-model="address">
      </div>
    </label>
    <div class="submit" @click="submit">保  存</div>
    <mt-popup
            v-model="popupVisible"
            position="bottom"
            style="width:100%">
      <div class="address">
        <div class="title">
          请选择
        </div>
        <div class="result">
          <span class="item" @click="selected = '1'" :class="{active: selected === '1'}">{{ addressProvince.name }}</span>
          <span class="item"  @click="selected = '2'" :class="{active: selected === '2'}">{{ addressCity.name }}</span>
          <span class="item"  @click="selected = '3'" :class="{active: selected === '3'}">{{ addressDistrict.name }}</span>
        </div>

        <div class="address_main">
          <mt-tab-container v-model="selected" >
            <mt-tab-container-item id="1">
             <div class="box">
               <div class="item" v-for="item in list_1" @click="addressProvince = item; selected = '2'">
                 {{item.name}}
               </div>
             </div>
            </mt-tab-container-item>
            <mt-tab-container-item id="2">
              <div class="box">
                <div class="item" v-for="item in list_2" @click="addressCity = item; selected = '3'">
                  {{item.name}}
                </div>
              </div>
            </mt-tab-container-item>
            <mt-tab-container-item id="3">
              <div class="box">
                <div class="item" v-for="item in list_3" @click="chooseFinish(item)">
                  {{item.name}}
                </div>
              </div>
            </mt-tab-container-item>
          </mt-tab-container>
        </div>

      </div>
    </mt-popup>
  </div>
</template>


<script>
  export default {
    data: function () {
      return {
        username: '',
        phone: '',
        addressProvince: {name: ''},
        addressCity: {name: ''},
        addressDistrict: {name: ''},
        address: '',
        popupVisible: false,
        selected: '1',
        list_1: [],
        list_2: [],
        list_3: []
      }
    },
    created: function () {
//      this.$store.commit('common_setTitle', '地址编辑')
//      获取省份
      this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getAreaSList' + this.$store.state.common.userId).then(function (data) {
//        console.log(data.body.data)
        this.list_1 = data.body.data
      }, function (req) {
        this.$toast(this.$store.state.common.tip.nointnet)
      })
      if (this.$route.query.from === 'edit') {
        this.getdata_edit()
      }
    },
    methods: {
      chooseFinish: function (item) {
        this.popupVisible = false
        this.addressDistrict = item
      },
      getdata_edit: function () {
//        var data = this.$route.query.data

        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserAddressInfo' +
                '&addressId=' + this.$route.query.address_id +
                this.$store.state.common.userId
        ).then(function (data) {
          data = data.body.data
          this.username = data.true_name
          this.phone = data.mob_phone
          this.addressProvince.name = data.province
          this.addressCity = data.cityinfo
          this.addressDistrict = data.areainfo
          this.address = data.address
        }, function (req) {
          this.$toast(this.$store.state.common.tip.nointnet)
        })
      },
      submit: function () {
        var url = ''
        var sendData = {
          name: this.username,
          province: this.addressProvince.name,
          cityId: this.addressCity.id,
          city: this.addressCity.name,
          areaId: this.addressDistrict.id,
          area: this.addressDistrict.name,
          address: this.address,
          phone: this.phone
        }
        if (this.username === '') {
          this.$toast('请输入收件人姓名')
          return false
        }
        if (this.phone === '') {
          this.$toast('请输入联系电话')
          return false
        }
        if (this.addressDistrict.name === '') {
          this.$toast('请输入完整的所在区域')
          return false
        }
        if (this.address === '') {
          this.$toast('请输入详细地址的地址')
          return false
        }
        if (this.$route.query.from === 'edit') {
          sendData.aid = this.$route.query.address_id
          url = '/index.php?act=mobileApi&op=postUserAddressEdit'
        } else if (this.$route.query.from === 'new') {
          url = '/index.php?act=mobileApi&op=postUserAddressAddition'
        }
        this.$http.post(this.$store.state.common.host + url + this.$store.state.common.userId, sendData).then(function (data) {
          console.log(data.body.data)
          if (data.body.code === '1000') {
            this.$router.go(-1)
          }
        }, function (req) {
          this.$toast(this.$store.state.common.tip.nointnet)
        })
      }
    },
    watch: {
      selected: function () {
        if (this.selected === '2') {
          this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getAreaCList' +
                  '&pro=' + this.addressProvince.id +
          this.$store.state.common.userId).then(function (data) {
            this.list_2 = data.body.data
            console.log(data.body.data)
          }, function (req) {
            this.$toast(this.$store.state.common.tip.nointnet)
          })
        } else
        if (this.selected === '3') {
          this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getAreaAList' +
                  '&city=' + this.addressCity.id +
          this.$store.state.common.userId).then(function (data) {
            this.list_3 = data.body.data
            console.log(data.body.data)
          }, function (req) {
            this.$toast(this.$store.state.common.tip.nointnet)
          })
        }
      }
    }
  }
</script>
<style scoped>
  .main{
    padding-bottom: 4rem;
    box-sizing: border-box;
  }
  .mint-navbar .mint-tab-item.is-selected{
    color: #FC963B!important;
    border-bottom:3px solid #FC963B!important;
  }
  .item_input{
    height: 3rem;
    line-height: 3rem;
    padding: 0 1rem;
    background: white;
    padding-left: 8rem;
    border-bottom: 1px solid #ccc;
    padding-right: 3rem;
    position: relative;
  }
  .item_input .right{
    display: inline-block;
    position: absolute;
    right: 1rem;
    top: 0.8rem;
  }
  .item_input .name{
    display: inline-block;
    width: 6rem;
    position: absolute;
    left: 1rem;
    color: #999;
  }
  .item_input input{
    border: 0
  }
  .item_input input:focus{
    border: 0;
    outline:none
  }
  .address{
    width: 100%;
    position: relative;
  }
  .address .title{
    height: 3rem;
    line-height: 3rem;
    width: 100%;
    background: white;
    text-align: center;
  }
  .address .result{
    height: 2rem;
    line-height: 2rem;
    background: white;
    padding: 0 1rem;
    border-bottom: 1px solid #ccc;
  }
  .address .result .item{
    display: inline-block;
    float: left;
    width: 5rem;
    height: 2rem;
    text-align: center;
  }
  .address .address_main .box{
    margin-top: 1rem;
    height: 2rem;
    line-height: 2rem;
    height: 16rem;
    overflow: scroll;
  }
  .address .box .item{
    padding: 0 1rem;
  }
  .address .result .active{
    color: #F15F25!important;
    border-bottom: 1px solid #F15F25;
  }
  .submit{
    width: 100%;
    height: 3rem;
    line-height: 3rem;
    text-align: center;
    background: #F15F25;
    color: white;
    font-size: 1.2rem;
    position: fixed;
    bottom: 0;
  }
  .submit:active{
    opacity: 0.7;
  }
</style>



