<template>
  <div class="main">
    <div class="title">
      订单基本信息
    </div>
    <div class="section1">
      <div class="item">
        <span>订单标号：</span> 1212132132132
      </div>
      <div class="item">
        <span>订单标号：</span> 2012.12.12
      </div>
      <div class="item">
        <span>付款时间：</span> 2012.12.12
      </div>
      <div class="item">
        <span>配送时间：</span> 2012.12.12
      </div>
      <div class="item">
        <span>完成时间：</span> 2012.12.12
      </div>
    </div>

    <div class="title">
      商品信息
    </div>
    <div class="section2">
      <div class="good">
        <div class="img"></div>
        <div class="good_name">
          阿尔卑斯纯净和岁
        </div>
        <div class="good_num">
          ¥ 20 x 100
        </div>
      </div>
      <div class="bottom">123 <span class="right"> ¥ 2000</span></div>
    </div>
    <div class="title">
      收件人信息
    </div>
    <div class="section3">
      <div class="top">
        <span>万佳乐</span> <span>13886215999</span> <span>第3次下单</span>
      </div>
      <div class="bottom">
        <span>我这里是地址</span>
        <div class="right">
          <My-Button val="拨打电话"></My-Button>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
  import MyButton from '../../common/MyButton.vue'
  export default {
    data: function () {
      return {}
    },
    components: {MyButton},
    created: function () {
      this.$store.commit('common_setTitle', 'demo')
    },
    methods: {}
  }
</script>
<style scoped>
  .main >.title{
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
  }
  .section1{
    background: white;
    padding: 0.5rem 0;
  }
  .section1 .item{
    height: 2rem;
    line-height: 2rem;
    padding: 0 1rem;

  }
  .section1 .item span:nth-child(1){
    color: #999;
  }
  .section2 .good{
    padding: 1rem;
    padding-left: 8.5rem;
    position: relative;
    background: white;
    min-height: 8rem;
    box-sizing: border-box;
  }
  .section2 .good .img{
    height: 6rem;
    width: 6rem;
    position: absolute;
    top: 1rem;
    left: 1rem;
    background: red;
  }
  .section2 .good .good_num{
    margin-top: 1rem;
  }
  .section2 .bottom{
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
    background: white;
    border-top: 1px solid #ccc;
  }
  .section2 .bottom .right{
    float: right;
    color: #FC2D2D;
  }
  .section3{
    background: white;
    padding: 0.5rem 0;
  }
  .section3 .top{
    padding: 0 1rem;
  }
  .section3 .top >span{
    display: inline-block;
    margin-right: 1rem;
    height: 2rem;
    line-height: 2rem;
  }
  .section3 .bottom{
    padding: 0 1rem;
    min-height: 2rem;
    line-height: 2rem;
    padding-right: 8rem;
    position: relative;
  }
  .section3 .bottom .right{
    position: absolute;
    right: 1rem;
    top: 0;
  }
</style>



