<template>
    <div class="page-navbar">
        <mt-navbar class="page-part" v-model="state" >
            <mt-tab-item id="0">全部</mt-tab-item>
            <mt-tab-item id="10">未支付</mt-tab-item>
            <mt-tab-item id="20">待配送</mt-tab-item>
            <mt-tab-item id="30">配送中</mt-tab-item>
            <mt-tab-item id="40">已收货</mt-tab-item>
        </mt-navbar>

        <!-- tab-container -->
        <div class="box">
            <mt-tab-container v-model="state">
                <mt-tab-container-item id="0">
                    <!--<mt-cell v-for="n in 10" :title="'内容 ' + n"></mt-cell>-->
                    <ul
                        v-infinite-scroll="loadMore"
                        infinite-scroll-disabled="loading"
                        infinite-scroll-distance="10">
                        <li v-for="item in list_0" class="item">
                            <order-Item :data='item' @change="refresh"></order-Item>
                        </li>
                    </ul>
                </mt-tab-container-item>
                <mt-tab-container-item id="10">
                    <ul
                            v-infinite-scroll="loadMore"
                            infinite-scroll-disabled="loading"
                            infinite-scroll-distance="10">
                        <li v-for="item in list_10" class="item">
                            <order-Item :data='item' @change="refresh"></order-Item>
                        </li>
                    </ul>

                </mt-tab-container-item>
                <mt-tab-container-item id="20">
                    <ul
                            v-infinite-scroll="loadMore"
                            infinite-scroll-disabled="loading"
                            infinite-scroll-distance="10">
                        <li v-for="item in list_20" class="item">
                            <order-Item :data='item' @change="refresh"></order-Item>
                        </li>
                    </ul>
                </mt-tab-container-item>
                <mt-tab-container-item id="30">
                    <ul
                            v-infinite-scroll="loadMore"
                            infinite-scroll-disabled="loading"
                            infinite-scroll-distance="30">
                        <li v-for="item in list_30" class="item">
                            <order-Item :data='item' @change="refresh"></order-Item>
                        </li>
                    </ul>
                </mt-tab-container-item>
                <mt-tab-container-item id="40">
                    <ul
                            v-infinite-scroll="loadMore"
                            infinite-scroll-disabled="loading"
                            infinite-scroll-distance="10">
                        <li v-for="item in list_40" class="item">
                            <order-Item :data='item' @change="refresh"></order-Item>
                        </li>
                    </ul>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
    </div>
</template>

<script>
  import orderItem from './OrderItem'
  export default {
    components: { orderItem },
    data: function () {
      return {
        state: '0',
        list_0: [],
        list_10: [],
        list_20: [],
        list_30: [],
        list_40: []
      }
    },
    methods: {
      loadMore () {
        this.loading = true
        setTimeout(() => {
//          let last = this.list[this.list.length - 1]
//          for (let i = 1; i <= 10; i++) {
//            this.list.push(last + i)
//          }
//          this.loading = false
        }, 2500)
      },
      getdata: function (cb) {
        cb = cb || function () {}
        this.$http.get(this.$store.state.common.host + '/index.php?act=mobileApi&op=getUserGoodsOrderList&state=' + this.state + this.$store.state.common.userId).then(function (data) {
          if (data.body.code === '1000') {
            cb(data.body.data)
            console.log(data.body.data)
          } else {
            this.$toast(data.body.msg)
          }
        }, function (req) {
          this.$toast(this.$store.state.common.tip.nointnet)
        })
      },
      refresh: function () {
        var _this = this
        this.getdata(function (data) {
          if (_this.state === '0') {
            _this.list_0 = data
          } else if (_this.state === '10') {
            _this.list_10 = data
          } else if (_this.state === '20') {
            _this.list_20 = data
          } else if (_this.state === '30') {
            _this.list_30 = data
          } else if (_this.state === '40') {
            _this.list_40 = data
          }
        })
      }
    },
    created: function () {
        //      设置TITLE
      this.$store.commit('common_setTitle', '进货单')
      var _this = this
      this.getdata(function (data) {
        _this.list_0 = data
      })
    },
    watch: {
      state: function () {
        var _this = this
        this.getdata(function (data) {
          if (_this.state === '0') {
            _this.list_0 = data
          } else if (_this.state === '10') {
            _this.list_10 = data
          } else if (_this.state === '20') {
            _this.list_20 = data
          } else if (_this.state === '30') {
            _this.list_30 = data
          } else if (_this.state === '40') {
            _this.list_40 = data
          }
        })
      }
    }
  }
</script>

<style scoped>
    .box{
        border-top: 0.5rem solid #EBEBEB;
    }
    .mint-navbar .mint-tab-item.is-selected{
        color: #FC963B!important;
        border-bottom:3px solid #FC963B!important;
    }

</style>



