<template>
  <div class="main">
    <div class="section">
      <div class="title">
        <i class="icon"></i>
        客户基本信息
      </div>
      <div class="item">
        会员昵称
        <span class="right">二狗子</span>
      </div>
      <div class="item">
        姓名
        <span class="right">二狗子</span>
      </div>
      <div class="item">
        会员手机
        <span class="right">13888888888</span>
      </div>
      <div class="item">
        固话
        <span class="right">0710-8888888</span>
      </div>
    </div>

    <div class="section">
      <div class="title">
        <i class="icon"></i>
        数据统计
      </div>
      <div class="item">
        购买次数
        <span class="right">500</span>
      </div>
      <div class="item">
        访问量
        <span class="right">3000</span>
      </div>
      <div class="item">
        用户来源
        <span class="right">微信商城</span>
      </div>
    </div>

    <div class="section section3">
      <div class="title">  <i class="icon"></i>配送信息</div>
      <div class="box">
        <div>
          <span>万佳乐</span><span>13888888888</span>
        </div>
        <div>
          <span>广东省深圳市罗湖区 大神范德萨发生大神大神</span>
        </div>
      </div>
      <input type="button" class="call" value="拨打电弧">
    </div>
  </div>
</template>


<script>
  export default {
    data: function () {
      return {}
    },
    created: function () {
      this.$store.commit('common_setTitle', 'demo')
    },
    methods: {}
  }
</script>
<style scoped>
  .section{
    background: white;
    margin-bottom: 1rem;
    border-top: 1px solid #ccc;
  }
  .section .title{
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
    border-bottom: 1px solid #ccc;
    color: #333;
  }
  .section .title .icon{
    height: 1.2rem;
    width: 0.2rem;
    display: inline-block;
    background: #F15F25;
    position: relative;
    top: 0.3rem;
    margin-right: 0.5rem;
  }
  .section .item{
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 1rem;
    padding-right: 10rem;
    font-weight: 500;
    border-bottom: 1px solid #ccc;
  }
  .section .item .right{
    display: inline-block;
    position: absolute;
    right: 1rem;
    color: #999;
  }
  .section3 .box{
    padding: 0.5rem 0;
  }
  .section3 .box >div{
    height: 1.5rem;
    line-height: 1.5rem;
    padding: 0 1rem;
  }
  .section3 .box >div:nth-child(2){
    color: #999;
  }
  .section3 .call{
    width: 90%;
    height: 3rem;
    margin:1rem;
    margin-left: 5%;
    margin-bottom: 1rem;
    display: inline-block;
    background: #F15F25;
    border: 0;
    border-radius: 0.3rem;
    color: white;
    font-size: 1.2rem;
  }
</style>



